import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import OtpInput from "react-otp-input";
import { createCompany } from "../../../features/auth/authAction";
import { resetAll } from "../../../features/auth/authSlice";

const CompanyDetailForm = () => {
  const { user, isLoading, isError, isSuccess, message } = useSelector(
    (state) => state.auth
  );

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "onTouched",
  });

  const [toggle, setToggle] = useState(false);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const onSubmit = (data, e) => {
    console.log(data);

    dispatch(createCompany(data));
  };

  useEffect(() => {
    if (isSuccess && user) {
      navigate(`/company/service`);
    }

    dispatch(resetAll());
  }, [isSuccess, isError, isLoading]);

  return (
    <div className="w-full h-full py-16">
      <div className="w-full h-full flex justify-center items-center flex-col">
        <div className="w-full md:w-2/3 h-48 flex justify-center items-center text-7xl px-5 text-center mb-5">
          <p>
            Tell us about your company <span className="text-teal-500">?</span>
          </p>
        </div>
        <div className="w-full md:w-1/2 h-full flex justify-center items-center">
          <form
            className="w-full px-5 h-auto flex flex-col justify-center space-y-3"
            onSubmit={handleSubmit(onSubmit)}
          >
            <div className="w-full flex justify-center items-center py-4 flex-col">
              <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Company Name
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900  focus:ring-sky-500 focus:border-sky-500  text-xl rounded-lg block w-full p-3 "
                  type="text"
                  placeholder="Enter Company address"
                  name="company-name"
                  {...register("name", {
                    required: "This field is required",
                    pattern: {
                      value: /^[a-zA-Z ]+$/,
                      message: "Enter a valid name",
                    },
                    minLength: {
                      value: 5,
                      message: "Enter a valid company name",
                    },
                    maxLength: {
                      value: 30,
                      message: "Reached Max length",
                    },
                  })}
                ></input>
                {errors.name && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.name.message}
                  </p>
                )}
              </div>

              <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Company Email
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900  focus:ring-sky-500 focus:border-sky-500  text-xl rounded-lg block w-full p-3 "
                  type="text"
                  placeholder="Enter Company email"
                  name="email"
                  {...register("email", {
                    required: "Email is required",
                    pattern: {
                      value: /^[a-zA-Z0-9_\-]{4,}[@][a-z]{3,}[\.][a-z]{1,3}$/i,
                      message: "This is not a valid email",
                    },
                  })}
                ></input>
                {errors.email && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.email.message}
                  </p>
                )}
              </div>

              <div className="w-full mb-5">
                <label
                  for="category"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Category
                </label>

                <select
                  id="countries"
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900  focus:ring-sky-500 focus:border-sky-500  text-xl rounded-lg block w-full p-3 "
                  {...register("category", { required: true })}
                >
                  <option selected>Select a category</option>
                  <option value="Health">Health</option>
                  <option value="Finance">Finance</option>
                  <option value="Education">Education</option>
                  <option value="Entertainment">Entertainment</option>
                  <option value="Shopping">Shopping</option>
                  <option value="Beauty">Beauty</option>
                  <option value="Technology">Technology</option>
                  <option value="Government Agency">Government Agency</option>
                  <option value="Other">Other</option>
                </select>

                {errors.categories && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.categories.message}
                  </p>
                )}
              </div>

              <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Address
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900  focus:ring-sky-500 focus:border-sky-500  text-xl rounded-lg block w-full p-3 "
                  type="text"
                  placeholder="Enter Compny Address"
                  name="address"
                  {...register("address", {
                    required: "This field is required",
                    pattern: {
                      value: /^[a-zA-Z ]+$/,
                      message: "Enter a valid address",
                    },
                    minLength: {
                      value: 5,
                      message: "Enter a valid address",
                    },
                    maxLength: {
                      value: 30,
                      message: "Reached Max length",
                    },
                  })}
                ></input>
                {errors.address && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.address.message}
                  </p>
                )}
              </div>

              <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Office Tel.
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900  focus:ring-sky-500 focus:border-sky-500  text-xl rounded-lg block w-full p-3 "
                  type="text"
                  placeholder="Enter Company telephone"
                  name="tel"
                  {...register("tel", {
                    required: "This field is required",
                    pattern: {
                      value: /^\+(?:[0-9] ?){6,14}[0-9]$/,
                      message: "Invalid phone format",
                    },
                  })}
                ></input>
                {errors.tel && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.tel.message}
                  </p>
                )}
              </div>

              <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  City
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900  focus:ring-sky-500 focus:border-sky-500  text-xl rounded-lg block w-full p-3 "
                  type="text"
                  placeholder="Enter City"
                  name="city"
                  {...register("city", {
                    required: "This field is  required",
                    pattern: {
                      value: /^[a-zA-Z ]+$/,
                      message: "Enter a valid city name",
                    },
                    minLength: {
                      value: 3,
                      message: "Enter a valid city name",
                    },
                    maxLength: {
                      value: 30,
                      message: "Reached Max length",
                    },
                  })}
                ></input>
                {errors.city && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.city.message}
                  </p>
                )}
              </div>

              <div className="w-full mb-5">
                <label
                  for="poBox"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  P.O. BOX
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900  focus:ring-sky-500 focus:border-sky-500  text-xl rounded-lg block w-full p-3 "
                  type="text"
                  placeholder="Enter P.O. box"
                  name="poBox"
                  {...register("poBox", {
                    required: "This field is required",
                    pattern: {
                      value: /^[0-9]*$/,
                      message: "Please enter a valid number",
                    },
                    minLength: {
                      value: 4,
                      message: "Enter a valid number",
                    },
                    maxLength: {
                      value: 6,
                      message: "Reached Max length",
                    },
                  })}
                ></input>
                {errors.poBox && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.poBox.message}
                  </p>
                )}
              </div>
            </div>
            <div className="w-full space-y-3">
              <button
                className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-2xl w-full px-5 py-4 text-center"
                type="submit"
              >
                Create Company
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default CompanyDetailForm;
