import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import {
  addService,
  uploadImage,
} from "../../../features/company/companyAction";

const UploadImageModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  // data from company store
  const { company } = useSelector((state) => state.company);

  const [submitLoad, setSubmitLoad] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const [countries, setCoutries] = useState();

  const watchField = watch("image");

  const dispatch = useDispatch();

  const onSubmit = (data) => {
    const formData = new FormData();
    formData.append("image", data.image[0]);
    dispatch(uploadImage(formData));

    reset();
  };

  const removeImage = () => {
    dispatch(uploadImage(null));
  };

  return (
    <>
      <input type="checkbox" id="upload-image-modal" className="modal-toggle" />
      <div className="modal">
        <div className="modal-box relative">
          <label
            htmlFor="upload-image-modal"
            className="btn btn-sm btn-circle absolute right-2 top-2"
          >
            
          </label>
          <h3 className="text-lg font-bold mb-6">Upload Image</h3>
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="mb-6">
              <div class="flex items-start justify-start w-full flex-col">
                <label
                  for="dropzone-file-add"
                  class="flex flex-col items-center justify-center w-full h-32 border-2 border-teal-500 border-dashed rounded-lg cursor-pointer bg-zinc-100 hover:bg-zinc-50"
                >
                  {watchField && watchField.length > 0 ? (
                    <div className="w-full h-full flex flex-col justify-center items-center">
                      <div className="w-full text-zinc-300">
                        {watchField[0].name}
                      </div>
                    </div>
                  ) : (
                    <div class="flex flex-col items-center justify-center pt-5 pb-6">
                      <i class="fa-solid fa-cloud-arrow-up fa-2xl text-teal-500 my-5"></i>

                      <p class="mb-2 text-sm text-teal-500 ">
                        <span class="font-semibold">Click to upload</span>
                      </p>
                      <p class="text-xs text-gray-500 dark:text-gray-400">
                        {/*  SVG, PNG, JPG or GIF (MAX. 800x400px) */}
                      </p>
                    </div>
                  )}

                  <input
                    {...register("image", {
                      required: true,
                      validate: {
                        validateFileFormat: (files) =>
                          ["image/jpeg", "image/png"].includes(
                            files[0]?.type
                          ) || "Only PNG, JPEG e GIF",
                      },
                    })}
                    id="dropzone-file-add"
                    type="file"
                    class="hidden"
                  />
                </label>
                {errors.image && (
                  <p className="text-red-400 text-sm">
                    * Field required/Unsupported file type.
                  </p>
                )}
              </div>
            </div>

            <div className="text-center mt-6">
              {submitLoad ? (
                <button
                  type="submit"
                  class="btn w-full disabled loading text-white bg-zinc-700 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 dark:bg-gray-800 dark:hover:bg-gray-700 dark:focus:ring-gray-700 dark:border-gray-700"
                ></button>
              ) : (
                <button
                  className="bg-teal-500 text-white active:bg-teal-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                  type="sumit"
                >
                  Upload Image
                </button>
              )}
            </div>
          </form>

          <div className="text-center mt-2">
            <button
              onClick={removeImage}
              className="bg-red-500 text-white  text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
              type="button"
            >
              Remove Image
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default UploadImageModal;
