import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";

const ViewCustomerModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const { services } = useSelector((state) => state.admin);
  const { customerForAction } = useSelector((state) => state.admin.utils);

  const dispatch = useDispatch();

  const onSubmit = (userData) => {
    const formData = new FormData();
  };

  useEffect(() => {
    console.log(customerForAction);
  }, [customerForAction]);

  return (
    <>
      {customerForAction && (
        <>
          <input
            type="checkbox"
            id="view-customer-modal"
            className="modal-toggle"
          />
          <div className="modal">
            <div className="modal-box relative">
              <label
                htmlFor="view-customer-modal"
                className="btn btn-sm btn-circle absolute right-2 top-2"
              >
                
              </label>
              <h3 className="text-lg font-bold mb-6">Customer Detail</h3>
              <div className="w-auto h-auto flex-col px-3">
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Name:{" "}
                    <span className="font-normal">
                      {" "}
                      {customerForAction[0].name}
                    </span>
                  </h2>
                </div>

                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Phone:{" "}
                    <span className="font-normal">
                      {" "}
                      {customerForAction[0].phone}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Address:{" "}
                    <span className="font-normal">
                      {customerForAction[0].address}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Queues:{" "}
                    <span className="font-normal"> customer queues</span>
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ViewCustomerModal;
