import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Outlet, useNavigate } from "react-router-dom";
import CustomerNavbar from "../components/Dashboard/Navbar/CustomerNavbar";
import CustomerFooter from "../views/Customer/Home/Footer";
import ViewServiceModal from "../views/Customer/Modals/viewServiceModal";
import ViewQueueModal from "../views/Customer/Modals/viewQueueModal";
import LeaveQueueModal from "../views/Customer/Modals/leaveQueueModal";
import EditProfileModal from "../views/Customer/Modals/editProfileModal";
import ChangePasswordModal from "../views/Customer/Modals/changePasswordModal";
import JoinQueueModal from "../views/Customer/Modals/joinQueueModal";
import {
  getCompanies,
  getCustomer,
  getServices,
} from "../features/customer/customerAction";
import LeaveQueueServiceModal from "../views/Customer/Modals/leaveQueueServiceModal";
import { resetAll } from "../features/customer/customerSlice";

import { toast, ToastContainer } from "react-toastify";
import MakeAppointmentModal from "../views/Customer/Modals/makeAppointmentModal";
import CancelAppointmentModal from "../views/Customer/Modals/cancelAppointmentModal";

const CustomerLayout = () => {
  const { user } = useSelector((state) => state.auth);

  const { customer } = useSelector((state) => state.customer);
  const { isLoading, isSuccess, isError, message } = useSelector(
    (state) => state.customer.status
  );

  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(getCompanies());
    dispatch(getServices());

    const interval = setInterval(() => {
      dispatch(getCustomer());
    }, 10000);

    return () => clearInterval(interval);
  }, [dispatch]);

  useEffect(() => {
    if (isError) {
      toast.error(message, {
        position: "bottom-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        className: "bg-zinc-800",
      });
    }

    if (isLoading) {
      //setSubmitLoad(true)
    }
    if (isSuccess) {
      toast.success(message, {
        position: "bottom-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        className: "bg-zinc-800",
      });
    }

    dispatch(resetAll());
  }, [isLoading, isError, isSuccess, message, customer]);

  useEffect(() => {
    if (!user) {
      navigate(`/auth/login`);
    }
  }, [user]);

  // get service detail

  return (
    <>
      <main>
        <CustomerNavbar />

        <ViewServiceModal />
        <ViewQueueModal />
        <JoinQueueModal />
        <LeaveQueueModal />
        <LeaveQueueServiceModal />
        <MakeAppointmentModal />
        <CancelAppointmentModal />

        <EditProfileModal />
        <ChangePasswordModal />

        <section className=" w-full h-auto">
          <Outlet />
        </section>
        <CustomerFooter />

        <ToastContainer
          position="bottom-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
          theme="light"
        />
        {/* Same as */}
      </main>
    </>
  );
};

export default CustomerLayout;
