import { Outlet, useNavigate } from "react-router-dom";
import AdminNavbar from "../components/Dashboard/Navbar/AdminNavbar";
import ChangePasswordModal from "../views/Admin/Modals/changePasswordModal";
import DeleteCustomerModal from "../views/Admin/Modals/deleteCustomerModal";
import DeleteCompanyModal from "../views/Admin/Modals/deleteCompanyModal";

import ViewCompanyModal from "../views/Admin/Modals/viewCompanyModal";
import ViewCustomerModal from "../views/Admin/Modals/viewCustomerModal";

import { useDispatch, useSelector } from "react-redux";
import { getAdmin } from "../features/admin/adminAction";
import { resetAll } from "../features/staff/staffSlice";
import { useEffect } from "react";
import UpdateProfileModal from "../views/Admin/Modals/updateProfileModal";

const AdminLayout = () => {
  const { user } = useSelector((state) => state.auth);

  const { admin, companies, customers } = useSelector((state) => state.admin);
  const { isLoading, isSuccess, isError } = useSelector(
    (state) => state.admin.status
  );
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(getAdmin());
  }, []);

  useEffect(() => {
    if (!user) {
      navigate("/auth/login");
    }

    dispatch(resetAll());
  }, [isLoading, isError, isSuccess, user]);

  return (
    <div className="w-full h-full">
      <AdminNavbar />

      <ViewCompanyModal />
      <ViewCustomerModal />

      <UpdateProfileModal />

      <ChangePasswordModal />
      <DeleteCompanyModal />
      <DeleteCustomerModal />
      <section className="w-full h-auto py-20">
        <Outlet />
      </section>
    </div>
  );
};

export default AdminLayout;
