import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";

const NewPassword = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "onTouched",
  });

  const navigate = useNavigate();
  const dispatch = useDispatch();

  const onSubmit = (data, e) => {};

  const [toggle, setToggle] = useState(false);
  return (
    <div className="w-full h-full py-16">
      <div className="w-full h-full flex justify-center items-center flex-col">
        <div className="w-full md:w-1/2 h-48 flex justify-center items-center text-7xl px-5">
          <p>
            Set new password<span className="text-teal-500">.</span>
          </p>
        </div>
        <div className="w-full md:w-1/2 h-full flex justify-center items-center">
          <form
            className="w-full px-5 h-auto flex flex-col justify-center space-y-3"
            onSubmit={handleSubmit(onSubmit)}
          >
            <div className="w-full relative space-y-3">
              <div className="w-full h-full">
                <i id="passlock" class="fa fa-eye" aria-hidden="true"></i>
                {toggle ? (
                  <i
                    id="passlock"
                    class="fa fa-eye-slash"
                    aria-hidden="true"
                    onClick={() => {
                      setToggle(!toggle);
                    }}
                  ></i>
                ) : (
                  <i
                    id="passlock"
                    class="fa fa-eye"
                    aria-hidden="true"
                    onClick={() => {
                      setToggle(!toggle);
                    }}
                  ></i>
                )}
                <input
                  className="bg-gray-50 border-2 border-gray-500 text-gray-900 text-xl rounded-lg block w-full p-4"
                  type={toggle ? "text" : "password"}
                  placeholder="Enter Password"
                  name="password"
                  {...register("password", {
                    required: "password is required",
                    minLength: {
                      value: 8,
                      message: "Password must be more than 8 characters",
                    },
                    maxLength: {
                      value: 14,
                      message: "Password cannot exceed more than 14 characters",
                    },
                  })}
                ></input>
                <p className="text-red-400">{errors.password?.message}</p>
              </div>
              <div className="w-full h-full">
                <i id="passlock" class="fa fa-eye" aria-hidden="true"></i>
                {toggle ? (
                  <i
                    id="passlock"
                    class="fa fa-eye-slash"
                    aria-hidden="true"
                    onClick={() => {
                      setToggle(!toggle);
                    }}
                  ></i>
                ) : (
                  <i
                    id="passlock"
                    class="fa fa-eye"
                    aria-hidden="true"
                    onClick={() => {
                      setToggle(!toggle);
                    }}
                  ></i>
                )}
                <input
                  className="bg-gray-50 border-2 border-gray-500 text-gray-900 text-xl rounded-lg block w-full p-4"
                  type={toggle ? "text" : "password"}
                  placeholder="Confirm Password"
                  name="password"
                  {...register("password", {
                    required: "password is required",
                    minLength: {
                      value: 8,
                      message: "Password must be more than 8 characters",
                    },
                    maxLength: {
                      value: 14,
                      message: "Password cannot exceed more than 14 characters",
                    },
                  })}
                ></input>
                <p className="text-red-400">{errors.password?.message}</p>
              </div>
            </div>

            <div className="w-full space-y-3">
              <button
                className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-2xl w-full px-5 py-4 text-center"
                type="submit"
              >
                Change Password
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default NewPassword;
