import { useDispatch, useSelector } from "react-redux";
import { Link, Outlet, useSearchParams } from "react-router-dom";
import { setCurrentStaffForAction } from "../../../features/company/companySlice";

const CompanyStaffMain = () => {
  return (
    <div className="w-full h-auto bg-zinc-100">
      <div className="w-full h-auto py-10 px-5 md:px-16 flex justify-center items-center flex-col space-y-2 bg-zinc-00">
        <CompanyStaffList />
      </div>
    </div>
  );
};

export const CompanyStaffList = () => {
  const { staff } = useSelector((state) => state.company);

  const dispatch = useDispatch();

  const setTheCurrentStaff = (data) => {
    dispatch(setCurrentStaffForAction(data));
  };

  return (
    <div className="w-full h-auto min-h-24 flex justify-center items-center py-4 ">
      <div className="w-full h-auto flex justify-evenly items-center flex-row flex-wrap space-y-4 ">
        <div className="w-full h-16 flex justify-start items-center py-5 rounded-lg px-2">
          <div className="w-full h-auto flex flex-row justify-start items-center space-x-2 ">
            <h1 className="text-3xl font-bold">Company Staff</h1>
          </div>
          <div className="md:w-48 h-8  flex justify-center items-center rounded-md space-x-2">
            <label
              htmlFor="add-staff-modal"
              className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-1  focus:ring-red-300 font-bold rounded-lg text-md w-full px-2 py-2 text-center"
            >
              Add Staff
            </label>
          </div>
        </div>
        <div className="w-full h-auto flex justify-evenly items-center flex-row flex-wrap py-10">
          {staff.length && (
            <table className="table-auto w-full border">
              <thead>
                <tr>
                  <th className="px-4 py-2 text-start">Name</th>
                  <th className="px-4 py-2 text-start ">Email</th>
                  <th className="px-4 py-2 text-start">Address</th>
                  <th className="px-4 py-2 text-center "></th>
                </tr>
              </thead>
              <tbody>
                {staff &&
                  staff.map((staff, index) => (
                    <tr
                      key={index}
                      className={index % 2 === 0 ? "bg-gray-100" : ""}
                    >
                      <td className=" px-4 py-2">{staff.name}</td>
                      <td className=" px-4 py-2">{staff.email}</td>
                      <td className=" px-4 py-2">{staff.address}</td>
                      <td className=" px-4 py-2 text-end">
                        <label
                          onClick={() => setTheCurrentStaff(staff._id)}
                          htmlFor="view-staff-modal"
                          className="bg-teal-500 hover:bg-teal-700 text-white font-bold py-2 px-4 rounded mr-2"
                        >
                          View
                        </label>
                        <label
                          onClick={() => setTheCurrentStaff(staff._id)}
                          htmlFor="edit-staff-modal"
                          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mr-2"
                        >
                          Edit
                        </label>
                        <button
                          onClick={() => setTheCurrentStaff(staff._id)}
                          className="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"
                        >
                          <a href="#delete-staff-modal">Delete</a>
                        </button>
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </div>
  );
};

export const CompanyStaffDetail = () => {
  return (
    <div className="w-full h-auto min-h-24 flex justify-center items-center py-4 bg-zinc-00  rounded-lg">
      <div className="w-full h-auto flex justify-start items-center flex-row flex-wrap bg-zinc-00">
        <div className="w-full h-16 flex justify-start items-center py-5 outline outline-1 outline-zinc-300 rounded-lg px-2">
          <div className="w-full h-auto flex flex-row justify-start items-center space-x-2 ">
            <Link
              to="/company/service/list"
              className="w-10 h-10 hover:bg-zinc-300 outline outline-1 outline-zinc-400 flex justify-center items-center rounded-md"
            >
              <i className="fa-solid fa-arrow-left m-1 fa-sm"></i>
            </Link>
            <h1 className="text-xl font-bold">Service Name Detail</h1>
          </div>
          <div className="w-1/2 h-8  flex justify-center items-center rounded-md space-x-2">
            <button className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-1  focus:ring-red-300 font-bold rounded-lg text-md w-full px-3 py-2 text-center">
              Edit Service
            </button>
            <button className="text-white bg-red-500 hover:bg-red-600 focus:ring-1  focus:ring-red-300 font-bold rounded-lg text-md w-full px-3 py-2 text-center">
              Remove Service
            </button>
          </div>
        </div>
        <div className="bg-zinc-00 w-full h-auto flex flex-col md:flex-row ">
          <div className="w-full md:w-1/2 h-72 bg-zinc-00 flex justify-start items-start p-2">
            <div className="w-full h-full flex justify-center items-start flex-col  pb-2 mb-3">
              <h1 className="text-xl font-bold text-teal-600 text-center">
                Visa Service
              </h1>
              <h1 className="text-md text-zinc-700 pt-2">
                Service Type: Appointment
              </h1>
              <h2 className="text-md text-zinc-700 ">Service Fee: 230 ETB</h2>
              <h2 className="text-md text-zinc-700">Clients per day: 12</h2>
              <h2 className="text-md text-zinc-700">
                Avg Waiting Time: 15 min
              </h2>
            </div>
          </div>
          <div className="w-full md:w-1/2 h-72 bg-zinc-00 flex justify-center items-start p-2">
            <div className="w-full h-full flex justify-center items-center flex-col p-2 mb-2 spacce-y-2">
              <div className="my-2 w-full h-16 flex justify-center items-center">
                <h1 className="text-2xl font-bold text-teal-600 text-center">
                  Remaining Time: 12:34:02
                </h1>
              </div>
              <h2 className="text-xl font-bold text-zinc-700 p-2">
                My Position: 45
              </h2>
              <h1 className="text-xl font-bold text-zinc-700">
                Current Position: 23
              </h1>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CompanyStaffMain;
