import { Outlet, useNavigate } from "react-router-dom";
import StaffNavbar from "../components/Dashboard/Navbar/StaffNavbar";
import CompleteQueueModal from "../views/Staff/Modals/completeQueueModal";
import DequeueCustomerModal from "../views/Staff/Modals/dequeueCustomerModal";
import NotifyCustomerModal from "../views/Staff/Modals/notifyCustomerModal";
import UpdateProfileModal from "../views/Staff/Modals/editProfileModal";
import ChangePasswordModal from "../views/Staff/Modals/changePasswordModal";
import { getStaff } from "../features/staff/staffAction";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { resetAll } from "../features/staff/staffSlice";
import { toast, ToastContainer } from "react-toastify";

const StaffLayout = () => {
  const { user } = useSelector((state) => state.auth);
  const { service, staff } = useSelector((state) => state.staff);
  const { isLoading, isSuccess, isError, message } = useSelector(
    (state) => state.staff.status
  );
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    const interval = setInterval(() => {
      dispatch(getStaff());
    }, 5000);

    return () => clearInterval(interval);
  }, [dispatch]);

  useEffect(() => {
    if (!user) {
      navigate("/auth/login");
    }

    if (isError) {
      toast.error(message, {
        position: "bottom-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        className: "bg-zinc-800",
      });
    }

    if (isLoading) {
      //setSubmitLoad(true)
    }
    if (isSuccess) {
      toast.success(message, {
        position: "bottom-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        className: "bg-zinc-800",
      });
    }

    dispatch(resetAll());
  }, [isLoading, isError, isSuccess, message, staff]);

  return (
    <div className="w-full h-full">
      <StaffNavbar />

      <CompleteQueueModal />
      <DequeueCustomerModal />
      <NotifyCustomerModal />
      <UpdateProfileModal />
      <ChangePasswordModal />
      <section className="w-full h-full pt-20">
        <Outlet />
      </section>

      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </div>
  );
};

export default StaffLayout;
