import { createSlice } from "@reduxjs/toolkit";
import {
  changePassword,
  getAdmin,
  removeCompany,
  removeCustomer,
  updateAdmin,
} from "./adminAction";

// Get user from localStorage
const admin = JSON.parse(localStorage.getItem("admin"));
const customers = JSON.parse(localStorage.getItem("customers"));
const companies = JSON.parse(localStorage.getItem("companies"));

const initialState = {
  admin: admin ? admin : null,
  customers: customers ? customers : null,
  companies: companies ? companies : null,
  utils: {
    customerForAction: null,
    companyForAction: null,
  },
  status: {
    isError: false,
    isSuccess: false,
    isLoading: false,
    message: "",
  },
};

export const adminSlice = createSlice({
  name: "admin",
  initialState,
  reducers: {
    resetAll: (state) => {
      state.status.isLoading = false;
      state.status.isSuccess = false;
      state.status.isError = false;
      state.status.message = "";
    },
    setCurrentCustomer: (state, action) => {
      state.utils.customerForAction = state.customers.filter(
        (customer) => customer._id === action.payload
      );
      console.log(action.payload);
    },
    setCurrentCompany: (state, action) => {
      state.utils.companyForAction = state.companies.filter(
        (company) => company._id === action.payload
      );
      console.log(action.payload);
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(getAdmin.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(getAdmin.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.admin = action.payload.admin;
        state.customers = action.payload.customers;
        state.companies = action.payload.companies;
        console.log(action.payload);
      })
      .addCase(getAdmin.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
      })
      .addCase(removeCustomer.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(removeCustomer.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.customers = action.payload;
        console.log(action.payload);
      })
      .addCase(removeCustomer.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
      })
      .addCase(removeCompany.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(removeCompany.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.companies = action.payload;
        console.log(action.payload);
      })
      .addCase(removeCompany.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
      })
      .addCase(updateAdmin.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(updateAdmin.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.admin = action.payload;
        console.log(action.payload);
      })
      .addCase(updateAdmin.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
        state.admin = null;
      })
      .addCase(changePassword.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(changePassword.fulfilled, (state, action) => {
        state.status.isLoading = false;
        //state.status.isAccountUpdated = true;
        state.status.message = "Password Changed";
        //state.myAccount = action.payload;
        console.log(action.payload);
      })
      .addCase(changePassword.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        //state.status.isAccountUpdateFailed = true;
        state.status.message = "Error Occured";
        state.status.message = action.payload;
      });
  },
});

export const { resetAll, setCurrentCustomer, setCurrentCompany } =
  adminSlice.actions;
export default adminSlice.reducer;
