import { Outlet } from "react-router-dom";
import Navbar from "../views/Home/Navbar";
import Footer from "../views/Home/Footer";

const HomeLayout = () => {
  return (
    <div className="w-full h-full">
      <Navbar />
      <section className="w-full h-full">
        <Outlet />
      </section>
      <Footer />
    </div>
  );
};

export default HomeLayout;
