import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Link, useLocation } from "react-router-dom";
import { logout } from "../../../features/auth/authAction";

const StaffNavbar = () => {
  const location = useLocation();

  const dispatch = useDispatch();

  const handleLogout = () => {
    dispatch(logout());
  };

  const [isOpen, setIsOpen] = useState();

  const toggleSidebar = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="w-full h-auto bg-cyan-0 backdrop-blur-lg  fixed z-50 shadow-sm">
      <div className="w-full h-20 relative flex justify-between shadow-sm">
        <div className="w-64 h-full flex justify-center items-center flex-row px-5 relative">
          <div className="w-full h-full flex justify-start items-center">
            <a href="/" className="text-zinc-600 text-4xl font-semibold">
              Queue<span className="text-teal-500">Ease</span>
            </a>
          </div>
        </div>

        <div className="w-full h-full flex md:hidden px-5 ">
          <div className="w-full flex justify-end items-center">
            <div
              onClick={toggleSidebar}
              className="w-12 h-12 flex justify-center items-center bg-white rounded-md outline outline-1 outline-teal-500"
            >
              {isOpen ? (
                <i className="fa-solid fa-xmark font-bold m-2 text-teal-600 text-2xl"></i>
              ) : (
                <i className="fa-solid fa-bars font-bold m-2 text-teal-600 text-2xl"></i>
              )}
            </div>
          </div>
        </div>

        <div
          className={`w-full duration-300 absolute ${
            isOpen ? "flex" : "hidden"
          } md:hidden h-64 bg-zinc-50 top-20 p-6}`}
        >
          <div className="w-full flex justify-center items-center  bg-zinc-00">
            <ul className="w-full flex flex-col space-y-5 justify-center items-center">
              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/staff"
                  className={
                    location.pathname.includes("/staff") ? " text-teal-500" : ""
                  }
                >
                  Queues
                </Link>
              </li>
              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/staff/profile"
                  className={
                    location.pathname.includes("/staff/profile")
                      ? " text-teal-500"
                      : ""
                  }
                >
                  Profile
                </Link>
              </li>

              <li className="">
                <Link className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-1  focus:ring-teal-300 font-bold rounded-md text-md w-24 px-2 py-2 text-center">
                  Logout
                </Link>
              </li>
            </ul>
          </div>
        </div>

        <div className="w-2/3 h-full md:flex px-5 hidden ">
          <div className="w-full flex justify-end items-center">
            <ul className="flex flex-row space-x-6 justify-center items-center">
              {/*  <li className="font-semibold">
                <Link
                  to="/customer"
                  className={
                    location.pathname === "/company" ? " text-teal-500" : ""
                  }
                >
                  Home
                </Link>
              </li> */}

              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/staff"
                  className={
                    location.pathname.includes("/staff") ? " text-teal-500" : ""
                  }
                >
                  Queues
                </Link>
              </li>
              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/staff/profile"
                  className={
                    location.pathname.includes("/staff/profile")
                      ? " text-teal-500"
                      : ""
                  }
                >
                  Profile
                </Link>
              </li>
              <li className="">
                <Link
                  onClick={handleLogout}
                  className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-1  focus:ring-teal-300 font-bold rounded-md text-md w-24 px-2 py-2 text-center"
                >
                  Logout
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default StaffNavbar;
