import { useDispatch, useSelector } from "react-redux";
import { Link, Outlet } from "react-router-dom";
import {
  setQueueForAction,
  setServiceForAction,
} from "../../../features/customer/customerSlice";

const CustomerQueueMain = () => {
  return (
    <div className="w-full h-auto bg-zinc-100 min-h-screen py-20">
      <div className="w-full h-full min-h-screen py-10 px-5 md:px-16 flex justify-start items-center flex-col space-y-2 bg-zinc-00">
        <div className="w-full h-auto flex justify-start items-center py-5">
          <div className="w-full h-auto">
            <h1 className="text-3xl font-bold">My Queues</h1>
          </div>
        </div>
        <Outlet />
      </div>
    </div>
  );
};

export const CustomerQueueList = () => {
  const { customer } = useSelector((state) => state.customer);

  const dispatch = useDispatch();

  const handleClickView = (queueId) => {
    console.log(queueId);
    dispatch(setQueueForAction(queueId));
  };

  const handleClickLeave = (serviceId) => {
    dispatch(setServiceForAction(serviceId));
    dispatch(setQueueForAction(serviceId));
  };
  return (
    <div className="w-full h-auto min-h-24 flex justify-center items-center py-4 ">
      <div className="w-full h-auto flex justify-evenly items-center flex-row flex-wrap space-y-4 ">
        <div className="w-full h-auto flex justify-evenly items-center flex-row flex-wrap py-5">
          {customer && customer.queues.length && (
            <div className="w-full h-auto flex justify-evenly items-center flex-row flex-wrap py-2">
              <table className="table-auto w-full border">
                <thead>
                  <tr>
                    <th className="px-4 py-2 text-start">Service name</th>
                    <th className="px-4 py-2 text-start">Type</th>
                    <th className="px-4 py-2 text-start">My Position</th>
                    <th className="px-4 py-2 text-start">Current Position</th>
                    <th className="px-4 py-2 text-start">Fee</th>
                    <th className="px-4 py-2 w-48 "></th>
                  </tr>
                </thead>
                <tbody>
                  {customer.queues.map((queue, index) => (
                    <tr
                      key={index}
                      className={index % 2 === 0 ? "bg-gray-100" : ""}
                    >
                      <td className=" px-4 py-2">
                        {queue.service && queue.service.name}
                      </td>
                      <td className=" px-4 py-2">
                        {queue.service && queue.service.type}
                      </td>
                      <td className=" px-4 py-2"> {queue.pos}</td>
                      <td className=" px-4 py-2">
                        {" "}
                        {queue.service && queue.service.currPos}
                      </td>
                      <td className=" px-4 py-2">
                        {" "}
                        {queue.service && queue.service.fee}
                        <span> ETB</span>
                      </td>
                      <td className=" px-4 py-2 text-center">
                        <label
                          onClick={() => handleClickView(queue.service._id)}
                          htmlFor="view-queue-modal"
                          className="bg-teal-500 hover:bg-teal-600 text-white font-bold py-2 px-4 rounded mr-2 cursor-pointer"
                        >
                          View
                        </label>

                        <button
                          onClick={() => handleClickLeave(queue.service._id)}
                          className="bg-red-500 hover:bg-red-600 text-white font-bold py-2 px-4 rounded"
                        >
                          <a href="#leave-queue-s-modal">Leave</a>
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export const CustomerQueueDetail = () => {
  return (
    <div className="w-full h-auto min-h-24 flex justify-center items-center py-4 bg-zinc-00  rounded-lg">
      <div className="w-full h-auto flex justify-start items-center flex-row flex-wrap bg-zinc-00">
        <div className="w-full h-16 flex justify-start items-center py-5 outline outline-1 outline-zinc-300 rounded-lg px-2">
          <div className="w-full h-auto flex flex-row justify-start items-center space-x-2 ">
            <Link
              to="/customer/queues/list"
              className="w-10 h-10 hover:bg-zinc-300 outline outline-1 outline-zinc-400 flex justify-center items-center rounded-md"
            >
              <i className="fa-solid fa-arrow-left m-1 fa-sm"></i>
            </Link>
            <h1 className="text-xl font-bold">Queue Summary</h1>
          </div>
          <div className="w-48 h-8 hover:bg-zinc-300  flex justify-center items-center rounded-md">
            <button className="text-white bg-red-500 hover:bg-red-600 focus:ring-1  focus:ring-red-300 font-bold rounded-lg text-md w-full px-3 py-2 text-center">
              Leave Queue
            </button>
          </div>
        </div>
        <div className="bg-zinc-00 w-full h-auto flex flex-col md:flex-row ">
          <div className="w-full md:w-1/2 h-72 bg-zinc-00 flex justify-center items-start p-2"></div>
        </div>
      </div>
    </div>
  );
};

export default CustomerQueueMain;
