import axios from "axios";

const API_URL = `${process.env.REACT_APP_BACKEND_URL}/api/company/`;

const getCompany = async (token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.get(API_URL + "get-company", config);

  if (response.data) {
    localStorage.setItem("company", JSON.stringify(response.data));
  }

  return response.data;
};

const updateCompany = async (data, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.post(API_URL + "update-company", data, config);

  if (response.data) {
    //localStorage.setItem("service", JSON.stringify(response.data));
  }

  return response.data;
};

