import { useDispatch, useSelector } from "react-redux";
import { notifyCustomer } from "../../features/staff/staffAction";
import { setCurrentCustomer } from "../../features/staff/staffSlice";
import { useEffect } from "react";

const ServiceQueue = () => {
  const { service, company } = useSelector((state) => state.staff);

  const dispatch = useDispatch();

  const handleNotifyAndDequeue = (customerId) => {
    dispatch(setCurrentCustomer(customerId));
    console.log(customerId);
  };

  useEffect(() => {}, [service]);

  return (
    <div className="w-full min-h-screen bg-zinc-100">
      {service && company ? (
        <div className="w-full h-auto py-5 px-5 md:px-16 flex justify-center items-center flex-col space-y-2 bg-zinc-00">
          <div className="w-full h-auto flex justify-between items-center flex-col">
            <div className="w-full h-full bg-zinc-00 flex justify-center items-start py-10">
              <div className="w-full h-full flex justify-center items-center flex-col  pb-2 mb-3">
                <div className="my-2 w-48 h-48 flex justify-center items-center outline outline-1 outline-teal-600 rounded-full space-y-2">
                  {company.image ? (
                    <img
                      src={`${process.env.REACT_APP_BACKEND_URL}/uploads/${company.image} `}
                      alt=""
                      className="w-full h-auto rounded-full"
                    />
                  ) : (
                    <div className="w-full h-full rounded-full flex justify-center items-center">
                      <h1 className="font-bold text-6xl text-teal-500">
                        {company.name.charAt(0)}
                      </h1>
                    </div>
                  )}
                </div>
                <h1 className="text-2xl font-bold text-teal-600 text-center">
                  {company.name}
                </h1>
                <h1 className="text-lg font-semibold text-zinc-700">
                  {service.name}
                </h1>
                <h1 className="text-md font-semibold text-zinc-700">
                  {company.address}
                </h1>
              </div>
            </div>
            <div className="table-responsive w-full h-full flex justify-center items-center">
              {service.queue.length && (
                <table className="table-auto w-full border">
                  <thead>
                    <tr>
                      <th className="px-4 py-2 text-start">Customer Name</th>
                      <th className="px-4 py-2 text-start">Entry Time</th>
                      <th className="px-4 py-2 text-start">Contact Info</th>
                      <th className="px-4 py-2 text-center">Position</th>
                    </tr>
                  </thead>
                  <tbody>
                    {service.queue.map((serv, index) => (
                      <tr
                        key={index}
                        className={index % 2 === 0 ? "bg-gray-100" : ""}
                      >
                        <td className="border-none px-4 py-2">
                          {serv.customer.name}
                        </td>
                        <td className="border-none px-4 py-2">
                          {serv.joinTime}
                        </td>
                        <td className="border-none px-4 py-2">
                          {serv.customer.address}
                        </td>
                        <td className="border-none px-4 py-2">{serv.pos}</td>
                        <td className="border-none px-4 py-2 text-end space-x-2">
                          <button
                            onClick={() =>
                              handleNotifyAndDequeue(serv.customer.user)
                            }
                            className="bg-cyan-500 hover:bg-cyan-700 text-white font-bold py-2 px-4 rounded"
                          >
                            <a href="#notify-customer-modal">Notify</a>
                          </button>
                          {/* <button className="bg-teal-500 hover:bg-teal-700 text-white font-bold py-2 px-4 rounded">
                        <a href="#complete-queue-modal">Complete</a>{" "}
                      </button> */}

                          <button
                            onClick={() =>
                              handleNotifyAndDequeue(serv.customer.user)
                            }
                            className="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"
                          >
                            <a href="#dequeue-customer-modal">Dequeue</a>
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}
            </div>
          </div>
        </div>
      ) : (
        <div className="w-full h-full flex justify-between items-center flex-col">
          <div className="w-full h-full bg-zinc-00 flex justify-center items-start py-20">
            <h1 className="text-lg">You are not assigned to any service</h1>
          </div>
        </div>
      )}
    </div>
  );
};

export default ServiceQueue;
