import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import OtpInput from "react-otp-input";
import { createCustomer } from "../../../features/auth/authAction";
import { resetAll } from "../../../features/auth/authSlice";

const CustomerDetailForm = () => {
  const { user, isLoading, isError, isSuccess, message } = useSelector(
    (state) => state.auth
  );

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "onTouched",
  });

  const [toggle, setToggle] = useState(false);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const onSubmit = (data, e) => {
    dispatch(createCustomer(data));
  };

  useEffect(() => {
    if (isSuccess && user) {
      navigate(`/customer`);
    }

    dispatch(resetAll());
  }, [isSuccess, isError, isLoading]);

  return (
    <div className="w-full h-full py-16">
      <div className="w-full h-full flex justify-center items-center flex-col">
        <div className="w-full md:w-2/3 h-48 flex justify-center items-center text-7xl px-5 text-center mb-5">
          <p>
            Tell us about yourself<span className="text-teal-500">?</span>
          </p>
        </div>
        <div className="w-full md:w-1/2 h-full flex justify-center items-center">
          <form
            className="w-full px-5 h-auto flex flex-col justify-center space-y-3"
            onSubmit={handleSubmit(onSubmit)}
          >
            <div className="w-full flex justify-center items-center py-4 flex-col">
              <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Full Name
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-500 text-gray-900 text-xl rounded-lg block w-full p-4"
                  type="text"
                  placeholder="Enter full name"
                  name="name"
                  {...register("name", {
                    required: "This field is required",
                    pattern: {
                      value: /^[a-zA-Z ]+$/,
                      message: "Enter a valid name",
                    },
                    minLength: {
                      value: 5,
                      message: "Enter a valid name",
                    },
                    maxLength: {
                      value: 30,
                      message: "Reached Max length",
                    },
                  })}
                ></input>
                {errors.name && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.name.message}
                  </p>
                )}
              </div>

              <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Phone No.
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-500 text-gray-900 text-xl rounded-lg block w-full p-4"
                  type="text"
                  placeholder="eg.  +2514567789"
                  name="company-name"
                  {...register("phone", {
                    required: "This field is required",
                    pattern: {
                      value: /^\+(?:[0-9] ?){6,14}[0-9]$/,
                      message: "Invalid phone format",
                    },
                  })}
                ></input>
                {errors.phone && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.phone.message}
                  </p>
                )}
              </div>

              <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Address
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-500 text-gray-900 text-xl rounded-lg block w-full p-4"
                  type="text"
                  placeholder="Enter address"
                  name="company-name"
                  {...register("address", {
                    required: "This field is required",
                    pattern: {
                      value: /^[a-zA-Z ]+$/,
                      message: "Enter a valid address",
                    },
                    minLength: {
                      value: 4,
                      message: "Enter a valid address",
                    },
                    maxLength: {
                      value: 30,
                      message: "Reached Max length",
                    },
                  })}
                ></input>
                {errors.address && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.address.message}
                  </p>
                )}
              </div>

              {/* <div className="w-full mb-5">
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  City
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-500 text-gray-900 text-xl rounded-lg block w-full p-4"
                  type="text"
                  placeholder="Enter city"
                  name="company-name"
                ></input>
                <p className="city">{errors.city?.message}</p>
              </div> */}
            </div>
            <div className="w-full space-y-3">
              <button
                className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-2xl w-full px-5 py-4 text-center"
                type="submit"
              >
                Submit Detail
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default CustomerDetailForm;
