import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";

const ViewServiceModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const { services } = useSelector((state) => state.company);
  const { serviceForAction } = useSelector((state) => state.company.utils);

  const [submitLoad, setSubmitLoad] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const [countries, setCoutries] = useState();

  const dispatch = useDispatch();

  const onSubmit = (userData) => {
    const formData = new FormData();
  };

  useEffect(() => {
    console.log(serviceForAction);
  }, [serviceForAction]);

  return (
    <>
      {serviceForAction && (
        <>
          <input
            type="checkbox"
            id="view-service-modal"
            className="modal-toggle"
          />
          <div className="modal">
            <div className="modal-box relative">
              <label
                htmlFor="view-service-modal"
                className="btn btn-sm btn-circle absolute right-2 top-2"
              >
                
              </label>
              <h3 className="text-lg font-bold mb-6">Service Detail</h3>
              <div className="w-auto h-auto flex-col px-3">
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Name:{" "}
                    <span className="font-normal">
                      {" "}
                      {serviceForAction[0].name}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Type:{" "}
                    <span className="font-normal">
                      {" "}
                      {serviceForAction[0].type}
                    </span>
                  </h2>
                </div>

                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Service Time:{" "}
                    <span className="font-normal">
                      {" "}
                      {serviceForAction[0].waitTime}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Working Hours:{" "}
                    {serviceForAction[0].type === "daily" ? (
                      <span className="font-normal"> 9:00AM - 5:00PM</span>
                    ) : (
                      <span className="font-normal">{`${serviceForAction[0].apptTime.start}:00 - ${serviceForAction[0].apptTime.end}:00`}</span>
                    )}
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Fee:{" "}
                    <span className="font-normal">
                      {" "}
                      {serviceForAction[0].fee}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Limit:{" "}
                    <span className="font-normal">
                      {" "}
                      {serviceForAction[0].limit}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Status: <span className="font-normal">Active</span>
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ViewServiceModal;
