import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { removeCustomer } from "../../../features/admin/adminAction";
//import { removeService } from "../../../features/company/companyAction";

const DeleteCustomerModal = () => {
  const { customerForAction } = useSelector((state) => state.admin.utils);

  const dispatch = useDispatch();

  useEffect(() => {
    console.log(customerForAction);
  }, [customerForAction]);

  const deleteCustomer = () => {
    dispatch(removeCustomer(customerForAction[0]));
  };

  return (
    <>
      <div className="modal" id="delete-customer-modal">
        <div className="modal-box">
          <h3 className="text-lg font-bold mb-6">Remove Customer</h3>
          <div className="w-auto h-auto flex-col text-sm">
            <div className="w-full h-8 flex flex-row justify-start items-center mb-5">
              <h2 className="text-md font-semibold text-zinc-800">
                Are you sure do you want to remove this customer?
              </h2>
            </div>
            <div className="w-full h-auto flex flex-row justify-start items-center">
              <div className="w-full flex justify-end space-x-3 modal-action ">
                <a
                  href="#"
                  className=" btn w-24 text-white bg-zinc-500 hover:bg-zinc-600 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 "
                >
                  Cancel
                </a>

                <a
                  href="#"
                  onClick={deleteCustomer}
                  className=" btn w-24 text-white bg-red-500 hover:bg-red-600 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 "
                >
                  Remove
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default DeleteCustomerModal;
