import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { addService } from "../../../features/company/companyAction";

const AddServiceModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  // data from company store
  const { staff } = useSelector((state) => state.company);

  const [submitLoad, setSubmitLoad] = useState(false);

  const dispatch = useDispatch();

  const watchField = watch("type");
  const watchField2 = watch("startTime");

  useEffect(() => {}, [watchField, watchField2]);

  const onSubmit = (userData) => {
    userData.apptTime = {
      start: userData.startTime,
      end: userData.endTime,
    };

    console.log(userData);
    dispatch(addService(userData));
  };

  return (
    <>
      <input type="checkbox" id="add-service-modal" className="modal-toggle" />
      <div className="modal">
        <div className="modal-box relative">
          <label
            htmlFor="add-service-modal"
            className="btn btn-sm btn-circle absolute right-2 top-2"
          >
            
          </label>
          <h3 className="text-lg font-bold mb-6">Add new service</h3>
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Service Name
              </label>
              <input
                type="text"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="Name"
                {...register("name", {
                  required: "Enter a valid service name",
                  pattern: {
                    value: /^[a-zA-Z ]+$/,
                    message: "Enter a valid name",
                  },
                  minLength: {
                    value: 3,
                    message: "Enter a valid name",
                  },
                  maxLength: {
                    value: 30,
                    message: "Reached Max length",
                  },
                })}
              />
              {errors.name && (
                <p className="text-red-400 text-sm py-2">
                  {errors.name.message}
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Service Type
              </label>

              <select
                id="small"
                class="block w-full p-2 text-sm border rounded-md hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500"
                {...register("type", { required: true })}
                defaultValue="daily"
              >
                <option value="daily">Daily</option>
                <option value="appointment">Appointment </option>
              </select>
            </div>

            {watchField !== "" && watchField !== "daily" ? (
              <>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Working Hours
                  </label>

                  <div className="w-full flex justify-between items-center flex-row space-x-1">
                    {/* <input
                      type="hidden"
                      {...register("apptTime", { required: true })}
                    ></input> */}
                    <select
                      id="small"
                      class="block w-full p-2 text-sm border rounded-md hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500"
                      {...register("startTime", {
                        required: "This field is required",
                      })}
                    >
                      <option selected> Start time </option>
                      <option value={3}>9:00AM </option>
                      <option value={4}>10:00AM </option>
                      <option value={5}>11:00AM </option>
                      <option value={6}>12:00AM </option>
                      <option value={7}>1:00PM </option>
                      <option value={8}>2:00PM </option>
                      <option value={9}>3:00APM </option>
                      <option value={10}>4:00PM </option>
                    </select>
                    {errors.startTime && (
                      <p className="text-red-400 text-sm py-2">
                        {errors.startTime.message}
                      </p>
                    )}
                    <select
                      id="small"
                      class="block w-full p-2 text-sm border rounded-md hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500"
                      {...register("endTime", {
                        required: "This field is required",
                      })}
                    >
                      <option selected> End time </option>
                      {watchField2 !== "" && watchField2 < 4 && (
                        <option value={10}>10:00AM </option>
                      )}
                      {watchField2 !== "" && watchField2 < 5 && (
                        <option value={11}>11:00AM </option>
                      )}
                      {watchField2 !== "" && watchField2 < 6 && (
                        <option value={12}>12:00AM </option>
                      )}
                      {watchField2 !== "" && watchField2 < 7 && (
                        <option value="1">1:00PM </option>
                      )}
                      {watchField2 !== "" && watchField2 < 8 && (
                        <option value="2">2:00PM </option>
                      )}
                      {watchField2 !== "" && watchField2 < 9 && (
                        <option value="3">3:00APM </option>
                      )}
                      {watchField2 !== "" && watchField2 < 10 && (
                        <option value="4">4:00PM </option>
                      )}
                      {watchField2 !== "" && watchField2 < 11 && (
                        <option value="5">5:00PM </option>
                      )}
                    </select>

                    {errors.endTime && (
                      <p className="text-red-400 text-sm py-2">
                        {errors.endTime.message}
                      </p>
                    )}
                  </div>
                </div>
              </>
            ) : (
              <></>
            )}

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Assign Staff
              </label>

              <select
                id="small"
                class="block w-full p-2 text-sm border   hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500 rounded-md"
                {...register("staff", { required: true })}
              >
                {staff &&
                  staff.map((stf) => {
                    return (
                      <>
                        {!stf.service && (
                          <option key={stf._id} value={stf._id}>
                            {stf.name}
                          </option>
                        )}
                      </>
                    );
                  })}
              </select>
              {errors.staff && (
                <p className="text-red-400 text-sm py-2">
                  * This field is required.
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Service Status
              </label>

              <select
                id="small"
                class="block w-full p-2 text-sm border   hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500 rounded-md"
                {...register("status", { required: true })}
              >
                <option value={true}>Active</option>
                <option value={false}>In Active</option>
              </select>
              {errors.status && (
                <p className="text-red-400 text-sm py-2">
                  * This field is required.
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Service Time(min)
              </label>
              <input
                type="number"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="Name"
                {...register("waitTime", {
                  required: "This field is required",
                  min: {
                    value: 1,
                    message: "invalid",
                  },
                  max: { value: 100, message: "value too large" },
                })}
              />
              {errors.waitTime && (
                <p className="text-red-400 text-sm py-2">
                  {errors.waitTime.message}
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Queue Limit
              </label>
              <input
                type="number"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="Enter queue limit"
                {...register("limit", {
                  required: "This field is required",
                  min: {
                    value: 2,
                    message: "invalid",
                  },
                  max: { value: 1000, message: "value too large" },
                })}
              />
              {errors.limit && (
                <p className="text-red-400 text-sm py-2">
                  {errors.limit.message}
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Service Fee
              </label>
              <input
                type="number"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="Enter service fee"
                {...register("fee", {
                  required: "This field is required",
                  min: {
                    value: 1,
                    message: "invalid",
                  },
                  max: { value: 10000, message: "value too large" },
                })}
              />
              {errors.fee && (
                <p className="text-red-400 text-sm py-2">
                  {errors.fee.message}
                </p>
              )}
            </div>

            <div className="text-center mt-6">
              {submitLoad ? (
                <button
                  type="submit"
                  class="btn w-full disabled loading text-white bg-zinc-700 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 dark:bg-gray-800 dark:hover:bg-gray-700 dark:focus:ring-gray-700 dark:border-gray-700"
                ></button>
              ) : (
                <button
                  className="bg-teal-500 text-white active:bg-teal-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                  type="sumit"
                >
                  Create Service
                </button>
              )}
            </div>
          </form>
        </div>
      </div>
    </>
  );
};

export default AddServiceModal;
