import { useEffect } from "react";
import { useSelector } from "react-redux";

const ViewQueueModal = () => {
  const { queueForAction } = useSelector((state) => state.customer.utils);

  useEffect(() => {}, [queueForAction]);

  return (
    <>
      {queueForAction && queueForAction[0] && (
        <>
          <input
            type="checkbox"
            id="view-queue-modal"
            className="modal-toggle"
          />
          <div className="modal">
            <div className="modal-box relative">
              <label
                htmlFor="view-queue-modal"
                className="btn btn-sm btn-circle absolute right-2 top-2"
              >
                
              </label>
              <h3 className="text-lg font-bold mb-6">Queue Detail</h3>
              <div className="w-auto h-auto flex-col px-3">
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Service Name:{" "}
                    <span className="font-normal">
                      {queueForAction[0].service.name}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Type:{" "}
                    <span className="font-normal">
                      {queueForAction[0].service.type}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Current Position:{" "}
                    <span className="font-normal">
                      {queueForAction[0].service.currPos}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    My Position:{" "}
                    <span className="font-normal">
                      {queueForAction[0].currPos - 1}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Entry Time:{" "}
                    <span className="font-normal">
                      {queueForAction[0].joinTime}
                    </span>
                  </h2>
                </div>
                {/*  <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Appointment Time:{" "}
                    <span className="font-normal">wewewewe</span>
                  </h2>
                </div> */}
                {/*  <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Remaining Time:{" "}
                    <span className="font-normal">wewewewe</span>
                  </h2>
                </div> */}
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Fee:{" "}
                    <span className="font-normal">
                      {queueForAction[0].service.fee}
                    </span>
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ViewQueueModal;
