import React from "react";
import { useDispatch } from "react-redux";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { logout } from "../../../features/auth/authAction";

const AdminNavbar = () => {
  const location = useLocation();

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleLogout = () => {
    dispatch(logout());
    //navigate("/auth/login");
  };

  return (
    <div className="w-full h-auto bg-cyan-0 backdrop-blur-lg  fixed z-50 shadow-sm">
      <div className="w-full h-20 relative flex justify-between shadow-sm">
        <div className="w-64 h-full flex justify-center items-center flex-row px-5 relative">
          <div className="w-full h-full flex justify-start items-center">
            <a href="/" className="text-zinc-600 text-4xl font-semibold">
              Queue<span className="text-teal-500">Ease</span>
            </a>
          </div>
        </div>

        <div className="w-full h-full flex md:hidden px-5 bg-zinc-100">
          <div className="w-full flex justify-end items-center">
            <div className="w-12 h-12 flex justify-center items-center bg-white">
              <i className="fa-solid fa- m-2 text-teal-600 text-md"></i>
            </div>
          </div>
        </div>

        <div className="w-full absolute hidden h-64 bg-zinc-100 top-20 p-6">
          <div className="w-full flex justify-center items-center  bg-zinc-00">
            <ul className="w-full flex flex-col space-y-4 justify-start items-start">
              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/admin/companies"
                  className={
                    location.pathname.includes("/admin/companies")
                      ? " text-teal-500"
                      : ""
                  }
                >
                  Companies
                </Link>
              </li>
              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/admin/customers"
                  className={
                    location.pathname.includes("/admin/customers")
                      ? " text-teal-500"
                      : ""
                  }
                >
                  Companies
                </Link>
              </li>

              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/customer/profile"
                  className={
                    location.pathname.includes("/customer/profile")
                      ? " text-teal-500"
                      : ""
                  }
                >
                  Profile
                </Link>
              </li>

              <li className="">
                <span
                  onClick={handleLogout}
                  className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-1  focus:ring-teal-300 font-bold rounded-md text-md w-24 px-2 py-2 text-center"
                >
                  Logout
                </span>
              </li>
            </ul>
          </div>
        </div>

        <div className="w-2/3 h-full md:flex px-5 hidden ">
          <div className="w-full flex justify-end items-center">
            <ul className="flex flex-row space-x-6 justify-center items-center">
              {/* <li className="font-semibold">
                <Link
                  to="/customer"
                  className={
                    location.pathname === "/customer" ? " text-teal-500" : ""
                  }
                >
                  Home
                </Link>
              </li> */}

              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/admin/companies"
                  className={
                    location.pathname.includes("/admin/companies")
                      ? " text-teal-500"
                      : ""
                  }
                >
                  Companies
                </Link>
              </li>
              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/admin/customers"
                  className={
                    location.pathname.includes("/admin/customers")
                      ? " text-teal-500"
                      : ""
                  }
                >
                  Customers
                </Link>
              </li>
              <li className="font-semibold hover:text-teal-500">
                <Link
                  to="/admin/profile"
                  className={
                    location.pathname.includes("/admin/profile")
                      ? " text-teal-500"
                      : ""
                  }
                >
                  Profile
                </Link>
              </li>

              <li className="">
                <Link
                  onClick={handleLogout}
                  className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-1  focus:ring-teal-300 font-bold rounded-md text-md w-24 px-2 py-2 text-center"
                >
                  Logout
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminNavbar;
