import { Outlet } from "react-router-dom";
import AuthNavbar from "../components/Dashboard/Navbar/AuthNavbar";
import { useEffect } from "react";
import { useSelector } from "react-redux";

import { toast, ToastContainer } from "react-toastify";

const AuthLayout = () => {
  const { user, message, isError, isSuccess, isLoading } = useSelector(
    (state) => state.auth
  );

  useEffect(() => {
    if (isError) {
      toast.error(message, {
        position: "bottom-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        className: "bg-zinc-800",
      });
    }

    if (isLoading) {
      //setSubmitLoad(true)
    }
    if (isSuccess) {
      toast.success(message, {
        position: "bottom-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        className: "bg-zinc-800",
      });
    }
  }, [message, isError, isSuccess, isLoading]);

  return (
    <div className="w-full h-full ">
      <AuthNavbar />
      <section className="w-full h-full pt-20">
        <Outlet />
      </section>

      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </div>
  );
};

export default AuthLayout;
