import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";

const ViewStaffModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const { staff } = useSelector((state) => state.company);
  const { staffForAction } = useSelector((state) => state.company.utils);

  const [submitLoad, setSubmitLoad] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const [countries, setCoutries] = useState();

  const dispatch = useDispatch();

  const onSubmit = (userData) => {
    const formData = new FormData();
  };

  useEffect(() => {
    console.log(staffForAction);
  }, [staffForAction]);

  return (
    <>
      {staffForAction && (
        <>
          <input
            type="checkbox"
            id="view-staff-modal"
            className="modal-toggle"
          />
          <div className="modal">
            <div className="modal-box relative">
              <label
                htmlFor="view-staff-modal"
                className="btn btn-sm btn-circle absolute right-2 top-2"
              >
                
              </label>
              <h3 className="text-lg font-bold mb-6">Staff Detail</h3>
              <div className="w-auto h-auto flex-col px-3">
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Name:{" "}
                    <span className="font-normal">
                      {staffForAction[0].name}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Email:{" "}
                    <span className="font-normal">
                      {staffForAction[0].email}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Phone:{" "}
                    <span className="font-normal">
                      {staffForAction[0].phone}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Address:{" "}
                    <span className="font-normal">
                      {" "}
                      {staffForAction[0].address}
                    </span>
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ViewStaffModal;
