import { createSlice } from "@reduxjs/toolkit";
import {
  changePassword,
  getCompanies,
  getCustomer,
  getServices,
  joinQueue,
  leaveQueue,
  makeAppointment,
  updateCustomer,
} from "./customerAction";

// Get user from localStorage
const customer = JSON.parse(localStorage.getItem("customer"));
const companies = JSON.parse(localStorage.getItem("companies"));
const services = JSON.parse(localStorage.getItem("services"));

const initialState = {
  customer: customer ? customer : null,
  companies: companies ? companies : null,
  services: services ? services : null,
  utils: {
    currentCompany: null,
    servicesForCurrentCompany: null,
    serviceForAction: null,
    queueForAction: null,
    searchQuery: null,
  },

  status: {
    isError: false,
    isSuccess: false,
    isLoading: false,
    message: "",
    fetchSucess: false,
    apptSuccess: false,
  },
};

export const customerSlice = createSlice({
  name: "customer",
  initialState,
  reducers: {
    resetAll: (state) => {
      state.status.isLoading = false;
      state.status.isSuccess = false;
      state.statusisError = false;
      state.status.isRegistered = false;
      state.status.message = "";
      state.status.fetchSucess = false;
      state.status.apptSuccess = false;
    },
    setCurrentCompany: (state, action) => {
      console.log(action.payload);
      state.utils.currentCompany = state.companies.filter(
        (co) => co._id === action.payload
      );
    },
    setServicesForCurrentCompany: (state, action) => {
      console.log("current company", action.payload);

      //compare company.services.serviceid and co .id

      /* state.servicesForCurrentCompany = state.services.filter(
        (co) => co._id === action.payload
      ); */

      state.utils.servicesForCurrentCompany = state.services.filter(
        (service) => service.company === action.payload
      );
    },
    setSearchQuery: (state, action) => {
      state.utils.searchQuery = action.payload;
    },
    setServiceForAction: (state, action) => {
      console.log(action.payload);
      state.utils.serviceForAction = state.services.filter(
        (service) => service._id === action.payload
      );
    },
    setQueueForAction: (state, action) => {
      console.log("heh");
      state.utils.queueForAction = state.customer.queues.filter(
        (queue) => queue.serviceId === action.payload
      );
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(getCustomer.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(getCustomer.fulfilled, (state, action) => {
        state.status.fetchSucess = true;

        state.customer = action.payload;
        console.log(action.payload);
      })
      .addCase(getCustomer.rejected, (state, action) => {
        state.status.isLoading = false;
      })
      .addCase(getCompanies.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(getCompanies.fulfilled, (state, action) => {
        state.companies = action.payload;
        state.status.isLoading = true;
      })
      .addCase(getCompanies.rejected, (state, action) => {
        state.status.message = action.payload;
      })
      .addCase(getServices.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(getServices.fulfilled, (state, action) => {
        state.services = action.payload;
        state.status.isLoading = true;
      })
      .addCase(getServices.rejected, (state, action) => {
        state.status.message = action.payload;
      })
      .addCase(joinQueue.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(joinQueue.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.customer = action.payload;
        state.status.message = "Join Success";
      })
      .addCase(joinQueue.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
        state.company = null;
      })
      .addCase(makeAppointment.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(makeAppointment.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.customer = action.payload;
        state.status.apptSuccess = true;
        state.status.message = "Join Success";
        console.log(action.payload);
      })
      .addCase(makeAppointment.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
      })
      .addCase(leaveQueue.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(leaveQueue.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.customer = action.payload;
        state.status.message = "Left from queue";

        console.log("left queue", action.payload);
      })
      .addCase(leaveQueue.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
      })
      .addCase(updateCustomer.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(updateCustomer.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.customer = action.payload;
        state.status.message = "Update Success";

        console.log(action.payload);
      })
      .addCase(updateCustomer.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
      })
      .addCase(changePassword.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(changePassword.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.status.message = "Password Changed";
      })
      .addCase(changePassword.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
      });
  },
});

export const {
  resetAll,
  setCurrentCompany,
  setServiceForAction,
  setQueueForAction,
  setSearchQuery,
  setServicesForCurrentCompany,
} = customerSlice.actions;

export const selectFilteredCompanies = (state) => {
  const query = state.customer.utils.searchQuery;
  const regex = new RegExp(query, "i");

  return state.customer.companies.filter((company) => regex.test(company.name));
};

export default customerSlice.reducer;
