import { createSlice } from "@reduxjs/toolkit";
import {
  addService,
  addStaff,
  changePassword,
  getCompany,
  removeService,
  removeStaff,
  updateCompany,
  updateService,
  updateStaff,
  uploadImage,
} from "./companyAction";

// Get user from localStorage
const store = JSON.parse(localStorage.getItem("company"));

const initialState = {
  company: store ? store.company : null,
  services: store ? store.services : null,
  staff: store ? store.staff : null,
  utils: {
    staffForAction: null,
    serviceForAction: null,
    searchQuery: null,
  },

  status: {
    isError: false,
    isSuccess: false,
    isLoading: false,
    message: "",
  },
};

export const companySlice = createSlice({
  name: "company",
  initialState,
  reducers: {
    resetAll: (state) => {
      state.status.isLoading = false;
      state.status.isSuccess = false;
      state.status.isError = false;
      state.status.isRegistered = false;
      state.status.message = "";
    },

    setCurrentStaffForAction: (state, action) => {
      console.log(action.payload);
      state.utils.staffForAction = state.staff.filter(
        (stf) => stf._id === action.payload
      );
    },
    setCurrentServiceForAction: (state, action) => {
      console.log(action.payload);
      state.utils.serviceForAction = null;
      state.utils.serviceForAction = state.services.filter(
        (serv) => serv._id === action.payload
      );
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(getCompany.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(getCompany.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.company = action.payload.company;
        state.staff = action.payload.staff;
        state.services = action.payload.services;
        console.log(action.payload);
      })
      .addCase(getCompany.rejected, (state, action) => {
        state.status.message = action.payload;
        state.company = null;
      })
      .addCase(updateCompany.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(updateCompany.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.company = action.payload;
        state.status.message = "Update success";
        console.log(action.payload);
      })
      .addCase(updateCompany.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
        state.company = null;
      })

      .addCase(addService.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(addService.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.services = action.payload.allServices;
        state.staff = action.payload.staff;
        state.status.message = "Service Added";

        console.log(action.payload);
      })
      .addCase(addService.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
        state.company = null;
      })
      .addCase(updateService.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(updateService.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.services = action.payload;
        state.status.message = "Update success";

        console.log(action.payload);
      })
      .addCase(updateService.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
        state.company = null;
      })
      .addCase(removeService.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(removeService.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.services = action.payload;
        console.log(action.payload);
      })
      .addCase(removeService.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
      })
      .addCase(addStaff.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(addStaff.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.staff = action.payload;
        console.log(action.payload);
        state.status.message = "Staff added";
      })
      .addCase(addStaff.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
      })
      .addCase(updateStaff.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(updateStaff.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.staff = action.payload;
        console.log(action.payload);
      })
      .addCase(updateStaff.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
      })
      .addCase(removeStaff.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(removeStaff.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.staff = action.payload;
        console.log(action.payload);
        state.status.message = "Staff removed";
      })
      .addCase(removeStaff.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
      })
      .addCase(uploadImage.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(uploadImage.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.company = action.payload;
        state.status.message = "Image uploaded";

        console.log("image uploaded", action.payload);
      })
      .addCase(uploadImage.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
        state.company = null;
      })
      .addCase(changePassword.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(changePassword.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.status.message = "Password Changed";
      })
      .addCase(changePassword.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
      });
  },
});

export const {
  resetAll,
  setCurrentStaffForAction,
  setCurrentServiceForAction,
} = companySlice.actions;

export default companySlice.reducer;
