import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { updateCompany } from "../../../features/company/companyAction";

const UpdateProfileModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  // default profile value from state

  const { company } = useSelector((state) => state.company);

  const [submitLoad, setSubmitLoad] = useState(false);

  const dispatch = useDispatch();

  const onSubmit = (userData) => {
    const formData = new FormData();
    dispatch(updateCompany(userData));
  };

  return (
    <>
      {company && (
        <>
          <input
            type="checkbox"
            id="edit-profile-modal"
            className="modal-toggle"
          />
          <div className="modal">
            <div className="modal-box relative">
              <label
                htmlFor="edit-profile-modal"
                className="btn btn-sm btn-circle absolute right-2 top-2"
              >
                
              </label>
              <h3 className="text-lg font-bold mb-6">Update Profile</h3>
              <form onSubmit={handleSubmit(onSubmit)}>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Company Name
                  </label>
                  <input
                    type="text"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Name"
                    {...register("name", {
                      required: "This field is required",
                      pattern: {
                        value: /^[a-zA-Z ]+$/,
                        message: "Enter a valid name",
                      },
                      maxLength: {
                        value: 30,
                        message: "Reached Max length",
                      },
                    })}
                    defaultValue={company.name}
                  />
                  {errors.name && (
                    <p className="text-red-400 text-sm py-2">
                      {errors.name.message}
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Category
                  </label>

                  <select
                    id="small"
                    class="block w-full p-2 text-sm border rounded-md hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500"
                    {...register("serviceType")}
                    defaultValue={company.category}
                  >
                    <option value="daily">Government Agency</option>
                    <option value="appointment">Beauty</option>
                    <option value="daily">Health</option>
                  </select>
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                    placeholder="Email"
                    defaultValue={company.email}
                    {...register("email", {
                      required: "Email is required",
                      pattern: {
                        value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                        message: "Invalid email format",
                      },
                    })}
                  />
                  {errors.email && (
                    <p className="text-red-400 text-sm py-2">
                      {errors.email.message}
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    City
                  </label>
                  <input
                    type="text"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                    placeholder="City"
                    defaultValue={company.city}
                    {...register("city", {
                      required: "This field is required",
                      pattern: {
                        value: /^[a-zA-Z ]+$/,
                        message: "Enter a valid name",
                      },
                      maxLength: {
                        value: 30,
                        message: "Reached Max length",
                      },
                    })}
                  />
                  {errors.city && (
                    <p className="text-red-400 text-sm py-2">
                      {errors.city.message}
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Address
                  </label>
                  <input
                    type="text"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                    placeholder="Address"
                    defaultValue={company.address}
                    {...register("address", {
                      required: "This field is required",
                      maxLength: {
                        value: 30,
                        message: "Reached Max length",
                      },
                    })}
                  />
                  {errors.address && (
                    <p className="text-red-400 text-sm py-2">
                      {errors.address.message}
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Phone Number
                  </label>
                  <input
                    type="text"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                    placeholder="+1234567898"
                    defaultValue={company.tel}
                    {...register("phoneNumber", {
                      required: "This field is required",
                      pattern: {
                        value: /^\+(?:[0-9] ?){6,14}[0-9]$/,
                        message: "Invalid phone format",
                      },
                    })}
                  />
                  {errors.phoneNumber && (
                    <p className="text-red-400 text-sm py-2">
                      {errors.phoneNumber.message}
                    </p>
                  )}
                </div>

                <div className="text-center mt-6">
                  {submitLoad ? (
                    <button
                      type="submit"
                      class="btn w-full disabled loading text-white bg-zinc-700 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 dark:bg-gray-800 dark:hover:bg-gray-700 dark:focus:ring-gray-700 dark:border-gray-700"
                    ></button>
                  ) : (
                    <button
                      className="bg-teal-500 text-white active:bg-zinc-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                      type="sumit"
                    >
                      Update Profile
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default UpdateProfileModal;
