import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { updateStaff } from "../../../features/company/companyAction";

const AddServiceModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const { staffForAction } = useSelector((state) => state.company.utils);

  const [submitLoad, setSubmitLoad] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const [countries, setCoutries] = useState();

  const dispatch = useDispatch();

  const onSubmit = (data) => {
    const userData = {
      id: staffForAction[0]._id,
      user: staffForAction[0].user,
      company: staffForAction[0].company,
      name: data.name,
      email: data.email,
      phone: data.phone,
      address: data.address,
    };

    console.log(userData);
    dispatch(updateStaff(userData));
  };

  useEffect(() => {
    console.log(staffForAction);
  }, [staffForAction]);

  return (
    <>
      {staffForAction && (
        <>
          <input
            type="checkbox"
            id="edit-staff-modal"
            className="modal-toggle"
          />
          <div className="modal">
            <div className="modal-box relative">
              <label
                htmlFor="edit-staff-modal"
                className="btn btn-sm btn-circle absolute right-2 top-2"
              >
                
              </label>
              <h3 className="text-lg font-bold mb-6">Update Staff Profile</h3>
              <form onSubmit={handleSubmit(onSubmit)}>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Staff Name
                  </label>
                  <input
                    type="text"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Name"
                    defaultValue={staffForAction[0].name}
                    {...register("name", { required: true })}
                  />
                  {errors.name && (
                    <p className="text-red-400 text-sm py-2">
                      * This field is required.
                    </p>
                  )}
                </div>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                    placeholder="Email"
                    defaultValue={staffForAction[0].email}
                    {...register("email", {
                      required: "Email is required",
                      pattern: {
                        value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                        message: "Invalid email format",
                      },
                    })}
                  />
                  {errors.email && (
                    <p className="text-red-400 text-sm py-2">
                      {errors.email.message}
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Address
                  </label>
                  <input
                    type="text"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Address"
                    defaultValue={staffForAction[0].address}
                    {...register("address", { required: true })}
                  />
                  {errors.name && (
                    <p className="text-red-400 text-sm py-2">
                      * This field is required.
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Phone No.
                  </label>
                  <input
                    type="text"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="+251908080808"
                    defaultValue={staffForAction[0].phone}
                    {...register("phone", { required: true })}
                  />
                  {errors.name && (
                    <p className="text-red-400 text-sm py-2">
                      * This field is required.
                    </p>
                  )}
                </div>

                <div className="text-end mt-6">
                  {submitLoad ? (
                    <button
                      type="submit"
                      class="btn w-full disabled loading text-white bg-zinc-700 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 dark:bg-gray-800 dark:hover:bg-gray-700 dark:focus:ring-gray-700 dark:border-gray-700"
                    ></button>
                  ) : (
                    <button
                      className="bg-teal-500 text-white active:bg-zinc-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-auto ease-linear transition-all duration-150"
                      type="sumit"
                    >
                      Update Staff
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default AddServiceModal;
