import React, { useState, useEffect, useRef } from "react";
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { registerUser } from "../../features/auth/authAction";
import { resetAll } from "../../features/auth/authSlice";

const Registration = () => {
  const { user, isLoading, isError, isSuccess, message } = useSelector(
    (state) => state.auth
  );

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    watch,
  } = useForm({
    mode: "onTouched",
  });

  const [toggle, setToggle] = useState(false);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const password = useRef({});
  password.current = watch("password", "");

  const onSubmit = (data, e) => {
    dispatch(registerUser(data));
    console.log(data);
  };

  useEffect(() => {
    if (isSuccess && user) {
      navigate(`/${user.role}`);
    }

    if (user && !user.isVerified) {
      navigate(`/auth/detail/${user.role}`);
    }

    dispatch(resetAll());
  }, [isSuccess, isError, isLoading]);

  return (
    <div className="w-full h-full py-16">
      <div className="w-full h-full flex justify-center items-center flex-col">
        <div className="w-full md:w-2/3 h-48 flex justify-center items-center text-7xl px-5 mb-5 text-center">
          <p>
            Hate Never Ending Queues <span className="text-teal-500">?</span>
          </p>
        </div>
        <div className="w-full md:w-1/2 h-full flex justify-center items-center">
          <form
            className="w-full px-5 h-auto flex flex-col justify-center"
            onSubmit={handleSubmit(onSubmit)}
          >
            <div className="w-full mb-5">
              <label
                for="company-name"
                class="block mb-2 text-xl font-medium text-gray-900"
              >
                Email
              </label>
              <input
                className="bg-gray-50 border-2 border-gray-300 text-gray-900 text-xl rounded-lg block w-full p-3"
                type="email"
                placeholder="Enter Email Address"
                name="email"
                {...register("email", {
                  required: "Email is required",
                  pattern: {
                    value: /^[a-zA-Z0-9_\-]{4,}[@][a-z]{3,}[\.][a-z]{1,3}$/i,
                    message: "This is not a valid email",
                  },
                })}
              ></input>
              <p className="text-red-400">{errors.email?.message}</p>
            </div>
            <div className="w-full relative space-y-4 mb-5">
              <div className="w-full h-auto">
                <label
                  for="password"
                  class="block mb-2 text-xl text-gray-900 font-normal"
                >
                  Password
                </label>
                <input
                  type="password"
                  id="password"
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900  focus:ring-sky-500 focus:border-sky-500  text-xl rounded-lg block w-full p-3 "
                  placeholder="Enter Password"
                  {...register("password", {
                    required: "You must specify a password",
                    minLength: {
                      value: 8,
                      message: "Password must have at least 8 characters",
                    },
                  })}
                />
                {errors.password && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.password.message}
                  </p>
                )}
              </div>

              <div className="w-full h-20 mb-6">
                <label
                  for="confirmNewPassword"
                  class="block mb-2 text-xl text-gray-900 font-normal"
                >
                  Confirm Password
                </label>
                <input
                  type="password"
                  id="confirmNewPassword"
                  className="bg-gray-50 border-2 border-gray-300 text-gray-900 text-xl rounded focus:ring-sky-500 focus:border-sky-500 block w-full p-3 "
                  placeholder="Confirm password"
                  {...register("confirmNewPassword", {
                    required: "You must specify a password",
                    validate: {
                      validatePassword: (value) =>
                        value === password.current || "Password do not match",
                    },
                    minLength: {
                      value: 8,
                      message: "Password must have at least 8 characters",
                    },
                  })}
                />
                {errors.confirmNewPassword && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.confirmNewPassword.message}
                  </p>
                )}
              </div>
            </div>

            <div className="w-full h-auto flex flex-col mb-5 mt-2">
              <label
                for="company-name"
                class="block mb-2 text-xl font-medium text-gray-900"
              >
                Register as
              </label>
              <div className="h-full flex flex-row justify-between text-lg space-x-5 px-3">
                <div className="h-full w-1/2">
                  <label className="" htmlFor="field-customer">
                    <input
                      {...register("aopt", {
                        required: "This field is required",
                      })}
                      type="radio"
                      name="aopt"
                      value="customer"
                      id="field-customer"
                    />
                    Customer
                  </label>
                </div>
                <div className="h-full w-1/2">
                  <label className="" htmlFor="field-store">
                    <input
                      {...register("aopt", {
                        required: "This field is required",
                      })}
                      type="radio"
                      name="aopt"
                      value="company"
                    />
                    Company
                  </label>
                </div>
              </div>
              <p className="text-red-400">{errors.aopt?.message}</p>
            </div>

            <div className="w-full mb-5">
              <button
                className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-2xl w-full px-3 py-3 text-center"
                type="submit"
              >
                Create Account
              </button>
            </div>
            <div className="w-full py-1 text-xl space-x-2">
              <span className="">Already have account?</span>
              <Link to="/auth/login" className="text-teal-500 underline">
                Login
              </Link>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Registration;
