const CompleteQueueModal = ({ tour }) => {
  return (
    <>
      <div className="modal" id="complete-queue-modal">
        <div className="modal-box">
          <h3 className="text-lg font-bold mb-6">Complete Customer Service</h3>
          <div className="w-auto h-auto flex-col text-sm">
            <div className="w-full h-8 flex flex-row justify-start items-center mb-5">
              <h2 className="text-md font-semibold text-zinc-800">
                Are you sure do you want to dequeue customer?
              </h2>
            </div>
            <div className="w-full h-auto flex flex-row justify-start items-center">
              <div className="w-full flex justify-end space-x-3 modal-action ">
                <a
                  href="#"
                  className=" btn w-24 text-white bg-zinc-500 hover:bg-zinc-600 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 "
                >
                  Cancel
                </a>

                <a
                  href="#"
                  className=" btn w-24 text-white bg-teal-500 hover:bg-teal-600 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 "
                >
                  Complete
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CompleteQueueModal;
