import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import {
  makeAppointment,
  updateCustomer,
} from "../../../features/customer/customerAction";

const MakeAppointmentModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const { customer } = useSelector((state) => state.customer);
  const { apptSuccess } = useSelector((state) => state.customer.status);
  const { serviceForAction } = useSelector((state) => state.customer.utils);

  const [submitLoad, setSubmitLoad] = useState(false);

  const dispatch = useDispatch();
  const watchField = watch("date");
  const onSubmit = (userData) => {
    console.log(userData);
    dispatch(makeAppointment({ data: userData, service: serviceForAction[0] }));
  };

  useEffect(() => {
    console.log(serviceForAction);
  }, [watchField]);

  useEffect(() => {
    if (apptSuccess) window.location.reload();
    console.log("here", apptSuccess);
  }, [apptSuccess]);
  return (
    <>
      {customer && serviceForAction && (
        <>
          <div className="modal" id="make-appt-modal">
            <div className="modal-box">
              <h3 className="text-lg font-bold mb-6">Schedule Appointment</h3>
              <form onSubmit={handleSubmit(onSubmit)}>
                <div className="relative w-full mb-3">
                  <div className="w-full flex justify-between items-center flex-row space-x-1">
                    <span className="w-1/2">
                      <label
                        className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        Pick Date
                      </label>
                      <input
                        type="date"
                        id="small"
                        class="block w-full p-2 text-sm border rounded-md hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500"
                        {...register("date", {
                          required: "This field is required",
                        })}
                      />
                      {errors.date && (
                        <p className="text-red-400 text-sm py-2">
                          {errors.date.message}
                        </p>
                      )}
                    </span>
                    <span className="w-1/2">
                      <label
                        className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        Pick Time
                      </label>
                      <select
                        id="small"
                        class="block w-full p-2 text-sm border rounded-md hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500"
                        {...register("time", { required: true })}
                        defaultValue={3}
                      >
                        <option value={3}>9:00AM </option>
                        <option value={4}>10:00AM </option>
                        <option value={5}>11:00AM </option>
                        <option value={6}>12:00AM </option>
                        <option value={7}>1:00PM </option>
                        <option value={8}>2:00PM </option>
                        <option value={9}>3:00APM </option>
                        <option value={10}>4:00PM </option>
                      </select>

                      {errors.time && (
                        <p className="text-red-400 text-sm py-2">
                          {errors.time.message}
                        </p>
                      )}
                    </span>
                  </div>
                </div>
                <div className="text-center mt-6 flex flex-row space-x-1">
                  <button
                    type="submit"
                    className="bg-teal-500 text-white active:bg-zinc-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-1/2 ease-linear transition-all duration-150"
                  >
                    Submit
                  </button>
                  <a
                    href="#"
                    className="bg-red-500 text-white active:bg-zinc-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-1/2 ease-linear transition-all duration-150"
                  >
                    Cancel
                  </a>
                </div>
              </form>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default MakeAppointmentModal;
