import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { resetAll } from "../../features/auth/authSlice";
import { loginUser } from "../../features/auth/authAction";

const Login = () => {
  const { user, isLoading, isError, isSuccess, message } = useSelector(
    (state) => state.auth
  );

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "onTouched",
  });

  const [toggle, setToggle] = useState(false);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const onSubmit = (data, e) => {
    console.log(data);
    dispatch(loginUser(data));
  };

  useEffect(() => {
    if (user && isSuccess) {
      navigate(`/${user.role}`);
    }

    dispatch(resetAll());
  }, [isSuccess, isError, isLoading, user]);

  return (
    <div className="w-full h-full py-16">
      <div className="w-full h-full flex justify-center items-center flex-col">
        <div className="w-full md:w-2/3 h-48 flex justify-center items-center text-7xl px-5 mb-5">
          <p>
            Welcome Back <span className="text-teal-500">!</span>
          </p>
        </div>
        <div className="w-full md:w-1/2 h-full flex justify-center items-center">
          <form
            className="w-full px-5 h-auto flex flex-col justify-center space-y-3"
            onSubmit={handleSubmit(onSubmit)}
          >
            <div className="w-full mb-5">
              <label
                for="company-name"
                class="block mb-2 text-xl font-medium text-gray-900"
              >
                Email
              </label>
              <input
                className="bg-gray-50 border-2 border-gray-500 text-gray-900 text-xl rounded-lg block w-full p-3"
                type="email"
                placeholder="Enter Email Address"
                name="email"
                {...register("email", {
                  required: "Email is required",
                  pattern: {
                    value: /^[a-zA-Z0-9_\-]{4,}[@][a-z]{3,}[\.][a-z]{1,3}$/i,
                    message: "This is not a valid email",
                  },
                })}
              ></input>
              {errors.email && (
                <p className="text-red-400 text-sm py-2">
                  {errors.email.message}
                </p>
              )}
            </div>
            <div className="w-full relative">
              <div className="w-full h-full mb-5">
                <i id="passlock" class="fa fa-eye" aria-hidden="true"></i>
                {toggle ? (
                  <i
                    id="passlock"
                    class="fa fa-eye-slash"
                    aria-hidden="true"
                    onClick={() => {
                      setToggle(!toggle);
                    }}
                  ></i>
                ) : (
                  <i
                    id="passlock"
                    class="fa fa-eye"
                    aria-hidden="true"
                    onClick={() => {
                      setToggle(!toggle);
                    }}
                  ></i>
                )}
                <label
                  for="company-name"
                  class="block mb-2 text-xl font-medium text-gray-900"
                >
                  Password
                </label>
                <input
                  className="bg-gray-50 border-2 border-gray-500 text-gray-900 text-xl rounded-lg block w-full p-3"
                  type={toggle ? "text" : "password"}
                  placeholder="Enter Password"
                  name="password"
                  {...register("password", {
                    required: "password is required",
                    minLength: {
                      value: 8,
                      message: "Password must be more than 8 characters",
                    },
                    maxLength: {
                      value: 14,
                      message: "Password cannot exceed 14 characters",
                    },
                  })}
                ></input>
                {errors.password && (
                  <p className="text-red-400 text-sm py-2">
                    {errors.password.message}
                  </p>
                )}
              </div>
            </div>
            {/* <div className="w-full h-auto flex flex-col mb-5">
              <label
                for="company-name"
                class="block mb-2 text-xl font-medium text-gray-900"
              >
                Login as
              </label>
              <div className="h-full flex flex-row justify-between text-lg space-x-5 px-4">
                <div className="h-full w-1/2">
                  <label className="" htmlFor="field-customer">
                    <input
                      {...register("aopt", {
                        required: "This field is required",
                      })}
                      type="radio"
                      name="aopt"
                      value="customer"
                      id="field-customer"
                    />
                    Customer
                  </label>
                </div>
                <div className="h-full w-1/2">
                  <label className="" htmlFor="field-store">
                    <input
                      {...register("aopt", {
                        required: "This field is required",
                      })}
                      type="radio"
                      name="aopt"
                      value="store"
                      id="field-store"
                    />
                    Store
                  </label>
                </div>
              </div>
              <p className="text-red-400">{errors.aopt?.message}</p>
            </div> */}
            <div className="w-full h-auto">
              <Link
                to="/auth/forgot-password"
                className="w-full text-teal-500 text-xl"
              >
                <u>Forgot password ?</u>
              </Link>
            </div>
            <div className="w-full space-y-3">
              <button
                className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-2xl w-full px-3 py-3 text-center"
                type="submit"
              >
                Login
              </button>
            </div>
            <div className="w-full py-1 text-xl space-x-2">
              <span className="">Create New Account</span>
              <Link to="/auth/register" className="text-teal-500 underline">
                Signup
              </Link>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Login;
