import { useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import {
  setCurrentCompany,
  setQueueForAction,
  setServiceForAction,
  setServicesForCurrentCompany,
} from "../../../features/customer/customerSlice";
import { useDispatch, useSelector } from "react-redux";

const CompanyDetail = () => {
  const { currentCompany, servicesForCurrentCompany, utils } = useSelector(
    (state) => state.customer.utils
  );

  const { customer } = useSelector((state) => state.customer);

  const dispatch = useDispatch();
  const { companyId } = useParams();

  useEffect(() => {
    console.log(companyId);
    dispatch(setCurrentCompany(companyId));

    // if curr co id is = co id

    dispatch(setServicesForCurrentCompany(companyId));
  }, [companyId]);

  const handleQueueAction = (serviceId) => {
    console.log(serviceId);
    dispatch(setServiceForAction(serviceId));
  };

  const handleQueueActionB = (serviceId) => {
    dispatch(setQueueForAction(serviceId));
    dispatch(setServiceForAction(serviceId));
  };

  useEffect(() => {
    //console.log(utils.serviceForAction);
  }, []);

  return (
    <>
      {currentCompany && servicesForCurrentCompany && (
        <div className="w-full min-h-screen bg-zinc-100 pt-20">
          <div className="w-full h-auto py-5 px-5 md:px-16 flex justify-center items-center flex-col space-y-2 bg-zinc-00">
            <div className="w-full h-auto flex justify-between items-center flex-col">
              <div className="w-full h-full bg-zinc-00 flex justify-center items-start py-10">
                <div className="w-full h-full flex justify-center items-center flex-col  pb-2 mb-3">
                  <div className="my-2 w-36 h-36 flex justify-center items-center outline outline-1 outline-teal-600 rounded-full space-y-2">
                    {currentCompany[0].image ? (
                      <img
                        src={`${process.env.REACT_APP_BACKEND_URL}/uploads/${currentCompany[0].image} `}
                        alt=""
                        className="w-full h-auto rounded-full"
                      />
                    ) : (
                      <div className="w-full h-full rounded-full flex justify-center items-center">
                        <h1 className="font-bold text-6xl text-teal-500">
                          {currentCompany[0].name.charAt(0)}
                        </h1>
                      </div>
                    )}
                  </div>
                  <h1 className="text-2xl font-bold text-teal-600 text-center">
                    {currentCompany[0].name}
                  </h1>
                  <h1 className="text-lg font-semibold text-zinc-700">
                    {currentCompany[0].address}
                  </h1>
                </div>
              </div>
              <div className="table-responsive w-full h-full flex justify-center items-center">
                {servicesForCurrentCompany && (
                  <table className="table-auto w-full border">
                    <thead>
                      <tr>
                        <th className="px-4 py-2 text-start">Service Name</th>
                        <th className="px-4 py-2 text-start">Service Time</th>
                        <th className="px-4 py-2 text-start">Type</th>
                        <th className="px-4 py-2 text-start">Fee</th>
                        <th className="px-4 py-2 text-start">Status</th>
                        <th className="px-4 py-2 text-start"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {servicesForCurrentCompany.map((service, index) => {
                        const isJoined =
                          customer &&
                          customer.queues.some(
                            (queue) => queue.serviceId === service._id
                          );
                        return (
                          <tr
                            key={index}
                            className={index % 2 === 0 ? "bg-gray-100" : ""}
                          >
                            <td className="border-none px-4 py-2">
                              {service.name}
                            </td>
                            <td className="border-none px-4 py-2">
                              {service.waitTime}
                              {""} Minutes
                            </td>
                            <td className="border-none px-4 py-2">
                              {service.type}
                            </td>
                            <td className="border-none px-4 py-2">
                              {service.fee}
                            </td>

                            <td className="border-none px-4 py-2">
                              {" "}
                              {service.status ? (
                                <>
                                  <i className="fa-solid fa-circle-dot text-green-500"></i>
                                  Active
                                </>
                              ) : (
                                <>
                                  <i className="fa-solid fa-circle-dot text-red-500"></i>
                                  InActive
                                </>
                              )}
                            </td>
                            <td className="border-none px-4 py-2 text-center">
                              {service.status && (
                                <>
                                  {isJoined ? (
                                    <button
                                      onClick={() =>
                                        handleQueueActionB(service._id)
                                      }
                                      className="bg-red-500 hover:bg-red-600 text-white font-bold py-2 px-4 rounded"
                                    >
                                      {service.type === "appointment" ? (
                                        <a href="#cancel-appt-modal">
                                          Cancel Appointment
                                        </a>
                                      ) : (
                                        <a href="#leave-queue-s-modal">
                                          Leave Queue
                                        </a>
                                      )}
                                    </button>
                                  ) : (
                                    <button
                                      onClick={() =>
                                        handleQueueAction(service._id)
                                      }
                                      className="bg-teal-500 hover:bg-teal-700 text-white font-bold py-2 px-4 rounded"
                                    >
                                      {service.type === "appointment" ? (
                                        <a href="#make-appt-modal">
                                          Make Appointment
                                        </a>
                                      ) : (
                                        <a href="#join-queue-modal">
                                          Join Queue
                                        </a>
                                      )}
                                    </button>
                                  )}
                                </>
                              )}
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default CompanyDetail;
