import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { addStaff } from "../../../features/company/companyAction";

const AddStaffModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const [submitLoad, setSubmitLoad] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const [countries, setCoutries] = useState();

  const dispatch = useDispatch();

  const onSubmit = (userData) => {
    const formData = new FormData();

    dispatch(addStaff(userData));

    console.log(userData);
  };

  return (
    <>
      <input type="checkbox" id="add-staff-modal" className="modal-toggle" />
      <div className="modal">
        <div className="modal-box relative">
          <label
            htmlFor="add-staff-modal"
            className="btn btn-sm btn-circle absolute right-2 top-2"
          >
            
          </label>
          <h3 className="text-lg font-bold mb-6">Add new staff</h3>
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Full Name
              </label>
              <input
                type="text"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="Name"
                {...register("name", { required: true })}
              />
              {errors.name && (
                <p className="text-red-400 text-sm py-2">
                  * This field is required.
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Email
              </label>
              <input
                type="email"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                placeholder="Email"
                {...register("email", {
                  required: "Email is required",
                  pattern: {
                    value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                    message: "Invalid email format",
                  },
                })}
              />
              {errors.email && (
                <p className="text-red-400 text-sm py-2">
                  {errors.email.message}
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Password
              </label>
              <div className="flex w-full justify-between flex-row space-x-1">
                <input
                  type={showPassword ? "text" : "password"}
                  class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                  placeholder="Password"
                  {...register("password", {
                    required: "Password is required",
                    minLength: {
                      value: 8,
                      message: "Password must be at least 8 characters",
                    },
                  })}
                />
                <div
                  className=""
                  onClick={() => setShowPassword(!showPassword)}
                >
                  <div className="w-10 h-full flex items-center justify-center hover:bg-zinc-200 rounded-md cursor-pointer">
                    <i
                      className={`fa-solid ${
                        showPassword ? "fa-eye-slash " : "fa-eye"
                      } fa-md text-zinc-800`}
                    ></i>
                  </div>
                </div>
              </div>
              {errors.password && (
                <p className="text-red-400 text-sm py-2">
                  {errors.password.message}
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Residential Address
              </label>
              <input
                type="text"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                placeholder="Address"
                {...register("address", { required: true })}
              />
              {errors.address && (
                <p className="text-red-400 text-sm py-2">
                  * This field is required.
                </p>
              )}
            </div>

            <div className="relative w-full mb-3">
              <label
                className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Phone Number
              </label>
              <input
                type="text"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 "
                placeholder="+1234567898"
                {...register("phone", {
                  required: "This field is required",
                  pattern: {
                    value: /^\+(?:[0-9] ?){6,14}[0-9]$/,
                    message: "Invalid phone format",
                  },
                })}
              />
              {errors.phoneNumber && (
                <p className="text-red-400 text-sm py-2">
                  {errors.phoneNumber.message}
                </p>
              )}
            </div>

            <div className="text-center mt-6">
              {submitLoad ? (
                <button
                  type="submit"
                  class="btn w-full disabled loading text-white bg-zinc-700 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 dark:bg-gray-800 dark:hover:bg-gray-700 dark:focus:ring-gray-700 dark:border-gray-700"
                ></button>
              ) : (
                <button
                  className="bg-teal-500 text-white active:bg-zinc-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                  type="sumit"
                >
                  Add Staff
                </button>
              )}
            </div>
          </form>
        </div>
      </div>
    </>
  );
};

export default AddStaffModal;
