import axios from "axios";

const API_URL = `${process.env.REACT_APP_BACKEND_URL}/api/admin/`;

const getAdmin = async (token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.get(API_URL + "get-admin", config);

  if (response.data) {
    localStorage.setItem("admin", JSON.stringify(response.data.admin));
    localStorage.setItem("customers", JSON.stringify(response.data.customers));
    localStorage.setItem("companies", JSON.stringify(response.data.companies));
  }

  return response.data;
};

const updateAdmin = async (data, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.post(API_URL + "update-admin", data, config);

  if (response.data) {
    //localStorage.setItem("service", JSON.stringify(response.data));
  }

  return response.data;
};

const removeCustomer = async (data, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.post(API_URL + "remove-customer", data, config);

  if (response.data) {
    localStorage.setItem("customers", JSON.stringify(response.data));
  }

  return response.data;
};

const removeCompany = async (data, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.post(API_URL + "remove-company", data, config);

  if (response.data) {
    localStorage.setItem("companies", JSON.stringify(response.data));
  }

  return response.data;
};

const changePassword = async (userData, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.post(
    API_URL + "change-password",
    userData,
    config
  );

  if (response.data) {
    //localStorage.setItem("customer", JSON.stringify(response.data));
  }

  return response.data;
};

const adminService = {
  getAdmin,
  removeCustomer,
  removeCompany,
  changePassword,
  updateAdmin,
};

export default adminService;
