import { useDispatch, useSelector } from "react-redux";
import { dequeueCustomer } from "../../../features/staff/staffAction";

const DequeueCustomerModal = ({ tour }) => {
  const { currentCustomer } = useSelector((state) => state.staff.utils);
  const { service } = useSelector((state) => state.staff);

  const dispatch = useDispatch();

  const handleDequeueCustomer = () => {
    console.log(service, currentCustomer);
    dispatch(
      dequeueCustomer({ customer: currentCustomer[0], service: service })
    );
  };

  return (
    <>
      <div className="modal" id="dequeue-customer-modal">
        <div className="modal-box">
          <h3 className="text-lg font-bold mb-6">Dequeue Customer</h3>
          <div className="w-auto h-auto flex-col text-sm">
            <div className="w-full h-8 flex flex-row justify-start items-center mb-5">
              <h2 className="text-md font-semibold text-zinc-800">
                Are you sure do you want to dequeue customer?
              </h2>
            </div>
            <div className="w-full h-auto flex flex-row justify-start items-center">
              <div className="w-full flex justify-end space-x-3 modal-action ">
                <a
                  href="#"
                  className=" btn w-24 text-white bg-zinc-500 hover:bg-zinc-600 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 "
                >
                  Cancel
                </a>

                <a
                  onClick={handleDequeueCustomer}
                  href="#"
                  className=" btn w-24 text-white bg-red-500 hover:bg-red-600 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 "
                >
                  Submit
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default DequeueCustomerModal;
