import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getCompany } from "../../../features/company/companyAction";

const CompanyProfileMain = () => {
  return (
    <div className="w-full h-auto bg-zinc-100">
      <div className="w-full h-auto py-10 px-5 md:px-16 flex justify-center items-center flex-col space-y-2 bg-zinc-00">
        <div className="w-full h-auto flex justify-start items-center py-5">
          <div className="w-full h-auto">
            <h1 className="text-3xl font-bold">Company Profile</h1>
          </div>
        </div>
        <div className="w-full h-auto flex justify-start items-center bg-zinc-00 flex-col space-y-2 px-5 outline outline-1 outline-zinc-300 rounded-lg">
          <ProfileHeader />
          <ProfileInformation />
          <AccountSetting />
        </div>
      </div>
    </div>
  );
};

export const ProfileHeader = () => {
  const { company } = useSelector((state) => state.company);

  return (
    <>
      {company && (
        <div className="w-full h-auto flex justify-between items-center py-2">
          <div className="w-full h-auto flex md:flex-row flex-col justify-start items-center ">
            <div className="w-36 h-36 flex justify-center items-center p-2">
              <label
                htmlFor="upload-image-modal"
                className="w-full h-full rounded-full  relative group cursor-pointer border-2 border-teal-500"
              >
                {company.image ? (
                  <img
                    src={`${process.env.REACT_APP_BACKEND_URL}/uploads/${company.image} `}
                    alt=""
                    className="w-full h-auto rounded-full"
                  />
                ) : (
                  <div className="w-full h-full rounded-full flex justify-center items-center">
                    <h1 className="font-bold text-6xl text-teal-500">
                      {company.name.charAt(0)}
                    </h1>
                  </div>
                )}
                <div className="w-full hidden group-hover:flex justify-center items-center h-full bg-zinc-50 hover:bg-zinc-800 hover:bg-opacity-75 bg-opacity-30 absolute bottom-0 rounded-full duration-300">
                  <i className="fa-solid fa-camera m-1 text-3xl text-teal-500"></i>
                </div>
              </label>
            </div>
            <div className="w-full md:w-1/2 h-full  flex flex-col justify-center items-center md:items-start px-4 ">
              <h1 className="text-2xl font-bold text-zinc-700">
                {company.name}
              </h1>
              <h1 className="text-md font- text-zinc-700">{company.name}</h1>
              <h1 className="text-md font text-zinc-700">{company.address}</h1>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export const ProfileInformation = () => {
  const { company } = useSelector((state) => state.company);

  useEffect(() => {
    console.log(company);
  }, [company]);

  return (
    <>
      {company && (
        <div className="w-full h-auto flex justify-between items-center flex-col ">
          <div className="w-full h-auto flex justify-between items-center py-5 ">
            <div className="w-full h-auto">
              <h1 className="text-lg font-bold">Profile Information</h1>
            </div>
            <label
              htmlFor="edit-profile-modal"
              className="w-24 h-8 hover:bg-zinc-300 outline outline-1 outline-zinc-400 flex justify-center items-center rounded-md"
            >
              <h1 className="text-md font-semibold text-zinc-600">
                <i className="fa-solid fa-pen-to-square m-1 fa-sm"></i>
                Edit
              </h1>
            </label>
          </div>
          <div className="w-full h-auto flex flex-col justify-start items-center bg-zinc-00 ">
            <div className="w-full h-auto  flex justify-center items-center p-2 flex-col md:flex-row md:space-x-2 mb-3">
              <div className="md:w-1/2 w-full h-full mb-2">
                <label
                  for="first_name"
                  class="block mb-2 text-md font-medium text-gray-900"
                >
                  Company Name
                </label>
                <h1 className="text-md font-bold text-zinc-700">
                  {company.name}
                </h1>
              </div>
              <div className="md:w-1/2 w-full h-full mb-2">
                <label
                  for="first_name"
                  class="block mb-2 text-md font-medium text-gray-900"
                >
                  Category
                </label>
                <h1 className="text-md font-bold text-zinc-700">
                  {company.category}
                </h1>
              </div>
            </div>
            <div className="w-full h-auto  flex justify-center items-center p-2 flex-col md:flex-row md:space-x-2 mb-3">
              <div className="md:w-1/2 w-full h-full mb-2">
                <label
                  for="first_name"
                  class="block mb-2 text-md font-medium text-gray-900"
                >
                  Email Address
                </label>
                <h1 className="text-md font-bold text-zinc-700">
                  {company.email}
                </h1>
              </div>
              <div className="md:w-1/2 w-full h-full mb-2">
                <label
                  for="first_name"
                  class="block mb-2 text-md font-medium text-gray-900"
                >
                  Phone
                </label>
                <h1 className="text-md font-bold text-zinc-700">
                  {company.tel}
                </h1>
              </div>
            </div>
            <div className="w-full h-auto  flex justify-center items-center p-2 flex-col md:flex-row md:space-x-2 mb-3">
              <div className="md:w-1/2 w-full h-full mb-2">
                <label
                  for="first_name"
                  class="block mb-2 text-md font-medium text-gray-900"
                >
                  Address
                </label>
                <h1 className="text-md font-bold text-zinc-700">
                  {company.address}
                </h1>
              </div>
              <div className="md:w-1/2 w-full h-full mb-2">
                <label
                  for="first_name"
                  class="block mb-2 text-md font-medium text-gray-900"
                >
                  City
                </label>
                <h1 className="text-md font-bold text-zinc-700">
                  {company.city}
                </h1>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export const AccountSetting = () => {
  return (
    <div className="w-full h-auto flex justify-between items-center flex-col ">
      <div className="w-full h-auto flex justify-between items-center py-4 ">
        <div className="w-full h-auto">
          <h1 className="text-lg font-bold">Account Setting</h1>
        </div>
      </div>
      <div className="w-full h-auto flex flex-col justify-start items-center  ">
        <div className="w-full h-auto  flex justify-center items-center p-2 flex-col md:flex-row">
          <div className="w-full h-full mb-2">
            <label
              for="first_name"
              class="block mb-2 text-md font-medium text-gray-900"
            >
              Change Password
            </label>
            <label
              htmlFor="change-password-modal"
              className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-1  focus:ring-teal-300 font-bold rounded-lg text-md w-auto px-3 py-2 text-center"
            >
              Change Password
            </label>
          </div>
          <div className="w-full h-full mb-2">
            <label
              for="first_name"
              class="block mb-2 text-md font-medium text-gray-900"
            >
              Close Account
            </label>
            <button className="text-white bg-red-500 hover:bg-red-600 focus:ring-1  focus:ring-red-300 font-bold rounded-lg text-md w-auto px-3 py-2 text-center">
              Close Account
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CompanyProfileMain;
