import { useDispatch, useSelector } from "react-redux";
import { leaveQueue } from "../../../features/customer/customerAction";

const LeaveQueueServiceModal = () => {
  const { queueForAction } = useSelector((state) => state.customer.utils);
  const { serviceForAction } = useSelector((state) => state.customer.utils);

  const dispatch = useDispatch();

  const handleLeaveQueue = () => {
    console.log("join or leave", queueForAction);
    dispatch(
      leaveQueue({
        queue: queueForAction && queueForAction[0],
        service: serviceForAction && serviceForAction[0],
      })
    );
  };
  return (
    <>
      <div className="modal" id="leave-queue-s-modal">
        <div className="modal-box">
          <h3 className="text-lg font-bold mb-6">Leave Queue</h3>
          <div className="w-auto h-auto flex-col text-sm">
            <div className="w-full h-8 flex flex-row justify-start items-center mb-5">
              <h2 className="text-md font-semibold text-zinc-800">
                Do you want to leave a queue for this service?
              </h2>
            </div>
            <div className="w-full h-auto flex flex-row justify-start items-center">
              <div className="w-full flex justify-end space-x-3 modal-action ">
                <a
                  href="#"
                  className=" btn w-24 text-white bg-zinc-500 hover:bg-zinc-600 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 "
                >
                  Cancel
                </a>

                <a
                  href="#"
                  onClick={handleLeaveQueue}
                  className=" btn w-24 text-white bg-red-500 hover:bg-red-600 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 "
                >
                  Leave
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default LeaveQueueServiceModal;
