import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { updateService } from "../../../features/company/companyAction";

const UpdateServiceModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const { staff } = useSelector((state) => state.company);
  const { serviceForAction } = useSelector((state) => state.company.utils);

  const [submitLoad, setSubmitLoad] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const [countries, setCoutries] = useState();

  const dispatch = useDispatch();

  const onSubmit = (data) => {
    const serviceData = {
      id: serviceForAction[0]._id,
      company: serviceForAction[0].company,
      name: data.name,
      type: data.serviceType,
      staff: data.staff,
      waitTime: data.waitTime,
      fee: data.fee,
      status: data.status,
    };
    console.log(serviceData);
    dispatch(updateService(serviceData));
  };

  useEffect(() => {
    console.log(serviceForAction);
  }, [serviceForAction]);

  return (
    <>
      {serviceForAction && (
        <>
          <input
            type="checkbox"
            id="edit-service-modal"
            className="modal-toggle"
          />
          <div className="modal">
            <div className="modal-box relative">
              <label
                htmlFor="edit-service-modal"
                className="btn btn-sm btn-circle absolute right-2 top-2"
              >
                
              </label>
              <h3 className="text-lg font-bold mb-6">Update Service</h3>
              <form onSubmit={handleSubmit(onSubmit)}>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Service Name
                  </label>
                  <input
                    type="text"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Name"
                    defaultValue={serviceForAction[0].name}
                    {...register("name", { required: true })}
                  />
                  {errors.name && (
                    <p className="text-red-400 text-sm py-2">
                      * This field is required.
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Service Type
                  </label>

                  <select
                    id="small"
                    class="block w-full p-2 text-sm border rounded-md hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500"
                    {...register("serviceType")}
                    defaultValue={serviceForAction[0].type}
                  >
                    <option value="appointment">Appointment </option>
                    <option value="daily">Daily</option>
                  </select>
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Assign Staff
                  </label>

                  <select
                    id="small"
                    class="block w-full p-2 text-sm border   hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500 rounded-md"
                    {...register("staff")}
                    defaultValue={serviceForAction[0].staff}
                  >
                    {staff &&
                      staff.map((stf) => {
                        return <option value={stf._id}>{stf.name} </option>;
                      })}
                  </select>
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Service Status
                  </label>

                  <select
                    id="small"
                    class="block w-full p-2 text-sm border   hover:ring-blue-500 hover:border-blue-500 bg-zinc-50 border-zinc-300 :placeholder-zinc-400 text-zinc-800 focus:ring-blue-500 focus:border-blue-500 rounded-md"
                    {...register("status", { required: true })}
                    defaultValue={serviceForAction[0].status}
                  >
                    <option value={true}>Active</option>
                    <option value={false}>In Active</option>
                  </select>
                  {errors.status && (
                    <p className="text-red-400 text-sm py-2">
                      * This field is required.
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Average Waiting Time
                  </label>
                  <input
                    type="number"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Name"
                    {...register("waitTime", { required: true })}
                    defaultValue={serviceForAction[0].waitTime}
                  />
                  {errors.waitTime && (
                    <p className="text-red-400 text-sm py-2">
                      * This field is required.
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Limit
                  </label>
                  <input
                    type="number"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Limit"
                    {...register("limit", { required: true })}
                    defaultValue={serviceForAction[0].limit}
                  />
                  {errors.limit && (
                    <p className="text-red-400 text-sm py-2">
                      * This field is required.
                    </p>
                  )}
                </div>

                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-zinc-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Fee
                  </label>
                  <input
                    type="number"
                    class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Name"
                    {...register("fee", { required: true })}
                    defaultValue={serviceForAction[0].fee}
                  />
                  {errors.fee && (
                    <p className="text-red-400 text-sm py-2">
                      * This field is required.
                    </p>
                  )}
                </div>

                <div className="text-center mt-6">
                  {submitLoad ? (
                    <button
                      type="submit"
                      class="btn w-full disabled loading text-white bg-zinc-700 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 dark:bg-gray-800 dark:hover:bg-gray-700 dark:focus:ring-gray-700 dark:border-gray-700"
                    ></button>
                  ) : (
                    <button
                      className="bg-teal-500 text-white active:bg-zinc-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                      type="sumit"
                    >
                      Update Service
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default UpdateServiceModal;
