import React, { useEffect } from "react";
import { Routes, Route, Outlet, Navigate } from "react-router-dom";
import SignUp from "./components/Auth/SignUp/SignUp";
import OtpOld from "./components/Auth/OTP/OTP";
import Forgot from "./components/Auth/ForgotPass/ForgotPass";
import ResetPass from "./components/Auth/ForgotPass/ResetPass";
import Details from "./components/Auth/Detail/Detail";
import QrScanner from "./components/Layout/QR-Code/QR-Scan";
import HomePage from "./components/Layout/Homepage/HomePage";
import StorePage from "./components/Layout/StorePage/StorePage";
import CreateStore from "./components/Layout/CreateStore/CreateStore";
import ViewQueue from "./components/Layout/ViewQueue/ViewQueue";
import Loader from "./components/Layout/Loaders/GifLoader";
import { useSelector } from "react-redux";
import Error404 from "./components/Layout/Error404/Error404";
import DemoLine from "./components/Layout/Charts/Chart";
import AuthLayout from "./layouts/AuthLayout";
import CustomerLayout from "./layouts/CustomerLayout";
import CompanyLayout from "./layouts/CompanyLayout";
import LoginOld from "./components/Auth/Login/Login";

import "react-toastify/dist/ReactToastify.css";

import Login from "./views/Auth/Login";
import Otp from "./views/Auth/Otp";
import Registration from "./views/Auth/Registration";
import ForgotPass from "./views/Auth/ForgotPass/ForgotPass";
import VerifyOtp from "./views/Auth/ForgotPass/VerifyOtp";
import NewPassword from "./views/Auth/ForgotPass/NewPassword";

import CustomerHome from "./views/Customer/Home";
import CustomerProfile from "./views/Customer/Profile";
import CustomerQueue from "./views/Customer/Queue";
import {
  CustomerQueueDetail,
  CustomerQueueList,
} from "./views/Customer/Queue/CustomerQueueMain";
import CompanyCustomerMain from "./views/Customer/Company/CompanyCustomerMain";
import CompanyCustomerSide from "./views/Customer/Company";

import CompanyService from "./views/Company/Service";
import CompanyServiceMain, {
  CompanyServiceDetail,
  CompanyServiceList,
} from "./views/Company/Service/CompanyServiceMain";
import CompanyProfile from "./views/Company/Profile";
import Home from "./views/Home";
import HomeLayout from "./layouts/HomeLayout";
import Team from "./views/Home/Team";
import About from "./views/Home/About";
import CompanyDetailForm from "./views/Auth/Detail/Company";
import CustomerDetailForm from "./views/Auth/Detail/Customer";
import CompanyDetail from "./views/Customer/Company/CompanyDetail";
import { CompanyStaffList } from "./views/Company/Staff/CompanyStaffMain";
import CompanyStaff from "./views/Company/Staff";
import StaffLayout from "./layouts/StaffLayout";
import ServiceQueue from "./views/Staff/Queue";
import StaffProfile from "./views/Staff/StaffProfile";
import AdminLayout from "./layouts/AdminLayout";
import AdminHome from "./views/Admin/Home";
import CompanyMain from "./views/Admin/Company/CompanyMain";
import AdminProfile from "./views/Admin/Profile";
import CustomerMain from "./views/Admin/Customer/CustomerMain";

function App() {
  let loader = useSelector((state) => state.LoaderReducer);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [loader]);
  return (
    <>
      {/* {loader[0]?<Loader/>:<></>} */}

      <Routes>
        {/* NEW ROUTES */}

        <Route path="auth" element={<AuthLayout />}>
          <Route path="login" element={<Login />} />
          <Route path="register" element={<Registration />} />
          <Route path="otp" element={<Otp />} />
          <Route path="forgot-password" element={<Outlet />}>
            <Route path="" element={<ForgotPass />} />
            <Route path="verify-otp" element={<VerifyOtp />} />
            <Route path="new-pass" element={<NewPassword />} />
          </Route>
          <Route path="detail" element={<Outlet />}>
            <Route path="customer" element={<CustomerDetailForm />} />
            <Route path="company" element={<CompanyDetailForm />} />
          </Route>
        </Route>

        <Route path="admin" element={<AdminLayout />}>
          <Route path="" element={<Navigate to="/admin/companies" />} />
          <Route path="companies" element={<CompanyMain />} />
          <Route path="customers" element={<CustomerMain />} />
          <Route path="profile" element={<AdminProfile />} />
        </Route>

        <Route path="customer" element={<CustomerLayout />}>
          <Route path="" element={<CustomerHome />} />
          <Route path="profile" element={<CustomerProfile />} />
          <Route path="queues" element={<CustomerQueue />}>
            <Route path="list" element={<CustomerQueueList />} />
            <Route path="queue" element={<Outlet />}>
              <Route path=":queueId" element={<CustomerQueueDetail />} />
            </Route>
          </Route>
          <Route path="companies" element={<CompanyCustomerSide />}>
            <Route path="list" element={<CompanyCustomerMain />} />
            <Route path=":companyId" element={<CompanyDetail />}>
              <Route path="view" element={<CompanyDetail />} />
              <Route path="service" element={<Outlet />}>
                <Route path=":serviceId" element={<Outlet />} />
              </Route>
            </Route>
          </Route>
          <Route path="setting" element={<Outlet />} />
        </Route>

        <Route path="company" element={<CompanyLayout />}>
          <Route path="" element={<Navigate to="/company/service" />} />
          <Route path="profile" element={<CompanyProfile />} />
          <Route path="staff" element={<CompanyStaff />} />
          <Route path="service" element={<CompanyService />} />
          <Route path="setting" element={<Outlet />} />
        </Route>

        {/* Staff Route */}
        <Route path="staff" element={<StaffLayout />}>
          <Route path="" element={<ServiceQueue />} />
          <Route path="profile" element={<StaffProfile />} />
        </Route>

        {/* OLD ROUTES */}
        {/* Auth Routes */}

        <Route exact path="/" element={<HomeLayout />}>
          <Route exact path="" element={<Home />} />
          <Route exact path="/about" element={<About />} />
        </Route>
      </Routes>
    </>
  );
}

export default App;
