import { createSlice } from "@reduxjs/toolkit";
import {
  getStaff,
  notifyCustomer,
  dequeueCustomer,
  changePassword,
  updateStaff,
} from "./staffAction";

// Get user from localStorage
const staff = JSON.parse(localStorage.getItem("staff"));
const service = JSON.parse(localStorage.getItem("service"));
const company = JSON.parse(localStorage.getItem("company"));

const initialState = {
  staff: staff ? staff : null,
  service: service ? service : null,
  company: company ? company : null,
  queues: service ? service.queue : null,
  utils: {
    currentCustomer: null,
  },
  status: {
    isError: false,
    isSuccess: false,
    isLoading: false,
    message: "",
  },
};

export const staffSlice = createSlice({
  name: "staff",
  initialState,
  reducers: {
    resetAll: (state) => {
      state.status.isLoading = false;
      state.status.isSuccess = false;
      state.status.isError = false;
      state.status.isRegistered = false;
      state.status.message = "";
    },
    setCurrentCustomer: (state, action) => {
      state.utils.currentCustomer = state.queues.filter(
        (customer) => customer.customerId === action.payload
      );
      console.log(action.payload);
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(getStaff.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(getStaff.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.staff = action.payload.staff;
        state.service = action.payload.service;
        state.company = action.payload.company;
      })
      .addCase(getStaff.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.message = action.payload;
      })
      .addCase(notifyCustomer.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(notifyCustomer.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        //state.staff = action.payload;
        state.status.message = "Notification is sent!";

        console.log(action.payload);
      })
      .addCase(notifyCustomer.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
        state.company = null;
      })
      .addCase(dequeueCustomer.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(dequeueCustomer.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.service = action.payload;
        state.status.message = "Customer removed from queue";

        console.log(action.payload);
      })
      .addCase(dequeueCustomer.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
        state.company = null;
      })
      .addCase(updateStaff.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(updateStaff.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.staff = action.payload;
        console.log(action.payload);
      })
      .addCase(updateStaff.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = action.payload;
        state.staff = null;
      })
      .addCase(changePassword.pending, (state) => {
        state.status.isLoading = true;
      })
      .addCase(changePassword.fulfilled, (state, action) => {
        state.status.isLoading = false;
        state.status.isSuccess = true;
        state.status.message = "Password Changed";
      })
      .addCase(changePassword.rejected, (state, action) => {
        state.status.isLoading = false;
        state.status.isError = true;
        state.status.message = "Error Occured";
      });
  },
});

export const { resetAll, setCurrentCustomer } = staffSlice.actions;
export default staffSlice.reducer;
