import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import OtpInput from "react-otp-input";

const VerifyOtp = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "onTouched",
  });

  const navigate = useNavigate();
  const dispatch = useDispatch();

  const onSubmit = (data, e) => {};

  const [toggle, setToggle] = useState(false);
  return (
    <div className="w-full h-full py-16">
      <div className="w-full h-full flex justify-center items-center flex-col">
        <div className="w-full md:w-1/2 h-48 flex justify-center items-center text-7xl px-5">
          <p>
            Verify Otp<span className="text-teal-500">.</span>
          </p>
        </div>
        <div className="w-full md:w-1/2 h-full flex justify-center items-center">
          <form
            className="w-full px-5 h-auto flex flex-col justify-center space-y-3"
            onSubmit={handleSubmit(onSubmit)}
          >
            <p className="w-full text-3xl">
              Enter OTP sent to your email
              <span className="text-teal-600">email@gmail.com</span>
            </p>
            <div className="w-full flex justify-center items-center py-4">
              <OtpInput
                numInputs={4}
                errorStyle={{ border: "3px solid red", borderRadius: "15px" }}
                inputStyle={{
                  height: "10vh",
                  border: "3px solid black",
                  borderRadius: "15px",
                  fontFamily: "Source Sans Pro",
                  fontWeight: "900",
                  fontSize: "3em",
                  color: "#304D6D",
                }}
                className="w-20 h-16  "
              />
            </div>
            <div className="w-full space-y-3">
              <button
                className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-2xl w-full px-5 py-4 text-center"
                type="submit"
              >
                Verify
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default VerifyOtp;
