const LeaveQueueModal = () => {

  return (
    <>
      <input type="checkbox" id="leave-queue-modal" className="modal-toggle" />
      <div className="modal">
        <div className="modal-box relative">
          <label
            htmlFor="leave-queue-modal"
            className="btn btn-sm btn-circle absolute right-2 top-2"
          >
            
          </label>
          <h3 className="text-lg font-bold mb-6"> Leave Queue </h3>
          
        </div>
      </div>
    </>
  );
};

