import axios from "axios";

const API_URL = `${process.env.REACT_APP_BACKEND_URL}/api/staff/`;

const getStaff = async (token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.get(API_URL + "get-staff", config);

  if (response.data) {
    localStorage.setItem("staff", JSON.stringify(response.data.staff));
    localStorage.setItem("service", JSON.stringify(response.data.service));
    localStorage.setItem("company", JSON.stringify(response.data.company));
  }

  return response.data;
};

