import { Outlet, useNavigate } from "react-router-dom";
import CompanyNavbar from "../components/Dashboard/Navbar/CompanyNavbar";
import AddServiceModal from "../views/Company/Modals/addServiceModal";
import ViewServiceModal from "../views/Company/Modals/viewServiceModal";
import EditServiceModal from "../views/Company/Modals/editServiceModal";
import DeleteServiceModal from "../views/Company/Modals/deleteServiceModal";
import EditStaffModal from "../views/Company/Modals/editStaffModal";
import DeleteStaffModal from "../views/Company/Modals/deleteStaffModal";
import AddStaffModal from "../views/Company/Modals/addStaffModal";
import EditProfileModal from "../views/Company/Modals/editProfileModal";
import ChangePasswordModal from "../views/Company/Modals/changePasswordModal";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { getCompany } from "../features/company/companyAction";
import ViewStaffModal from "../views/Company/Modals/viewStaffModal";
import Footer from "../components/Footer/Footer";
import { resetAll } from "../features/company/companySlice";
import UploadImageModal from "../views/Company/Modals/uploadImageModal";

import { toast, ToastContainer } from "react-toastify";

const CompanyLayout = () => {
  const { user } = useSelector((state) => state.auth);

  const { company, staff, services } = useSelector((state) => state.company);
  const { isLoading, isSuccess, isError, message } = useSelector(
    (state) => state.company.status
  );
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(getCompany());
  }, []);

  useEffect(() => {
    if (!user) {
      navigate("/auth/login");
    }

    if (isError) {
      toast.error(message, {
        position: "bottom-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        className: "bg-zinc-800",
      });
    }

    if (isLoading) {
      //setSubmitLoad(true)
    }
    if (isSuccess) {
      toast.success(message, {
        position: "bottom-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        className: "bg-zinc-800",
      });
    }

    dispatch(resetAll());
  }, [isLoading, isError, isSuccess, user, message]);
  return (
    <>
      <main>
        <CompanyNavbar />

        <AddServiceModal />
        <ViewServiceModal />
        <EditServiceModal />
        <DeleteServiceModal />

        <AddStaffModal />
        <ViewStaffModal />
        <EditStaffModal />
        <DeleteStaffModal />

        <EditProfileModal />
        <ChangePasswordModal />

        <UploadImageModal />

        <section className=" w-full h-auto min-h-screen pt-20 bg-zinc-100">
          <Outlet />
        </section>
        <Footer />
        <ToastContainer
          position="bottom-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
          theme="light"
        />
      </main>
    </>
  );
};

export default CompanyLayout;
