import { useRef, useState } from "react";
import { useForm } from "react-hook-form";
import { changePassword } from "../../../features/admin/adminAction";
import { useDispatch } from "react-redux";

const ChangePasswordModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const [submitLoad, setSubmitLoad] = useState(false);
  const password = useRef({});
  password.current = watch("newPassword", "");

  const dispatch = useDispatch();

  const onSubmit = (passwordData) => {
    dispatch(changePassword(passwordData));
    reset();
  };

  return (
    <>
      <input
        type="checkbox"
        id="change-password-modal"
        className="modal-toggle"
      />
      <div className="modal">
        <div className="modal-box relative">
          <label
            htmlFor="change-password-modal"
            className="btn btn-sm btn-circle absolute right-2 top-2"
          >
            
          </label>
          <h3 className="text-lg font-bold mb-6">Chnage Password</h3>
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="w-full h-auto">
              <label
                for="oldPassword"
                class="block mb-2 text-sm  text-gray-900 font-semibold"
              >
                Current Password
              </label>
              <input
                type="password"
                id="oldPassword"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded focus:ring-sky-500 focus:border-sky-500 block w-full p-2.5 "
                placeholder=""
                {...register("oldPassword", {
                  required: "You must specify a password",
                  minLength: {
                    value: 4,
                    message: "Password must have at least 8 characters",
                  },
                })}
              />
              {errors.oldPassword && (
                <p className="text-red-400 text-sm py-2">
                  {errors.oldPassword.message}
                </p>
              )}
            </div>

            <div className="w-full h-auto">
              <label
                for="newPassword"
                class="block mb-2 text-sm  text-gray-900 font-semibold"
              >
                New Password
              </label>
              <input
                type="password"
                id="newPassword"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded focus:ring-sky-500 focus:border-sky-500 block w-full p-2.5 "
                placeholder=""
                {...register("newPassword", {
                  required: "You must specify a password",
                  minLength: {
                    value: 8,
                    message: "Password must have at least 8 characters",
                  },
                })}
              />
              {errors.newPassword && (
                <p className="text-red-400 text-sm py-2">
                  {errors.newPassword.message}
                </p>
              )}
            </div>

            <div className="w-full h-20">
              <label
                for="confirmNewPassword"
                class="block mb-2 text-sm  text-gray-900 font-semibold"
              >
                Confirm Password
              </label>
              <input
                type="password"
                id="confirmNewPassword"
                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded focus:ring-sky-500 focus:border-sky-500 block w-full p-2.5 "
                placeholder=""
                {...register("confirmNewPassword", {
                  required: "You must specify a password",
                  validate: {
                    validatePassword: (value) =>
                      value === password.current || "Password do not match",
                  },
                  minLength: {
                    value: 8,
                    message: "Password must have at least 8 characters",
                  },
                })}
              />
              {errors.confirmNewPassword && (
                <p className="text-red-400 text-sm py-2">
                  {errors.confirmNewPassword.message}
                </p>
              )}
            </div>
            <div className="text-center mt-6">
              {submitLoad ? (
                <button
                  type="submit"
                  class="btn w-full disabled loading text-white bg-zinc-700 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2 dark:bg-gray-800 dark:hover:bg-gray-700 dark:focus:ring-gray-700 dark:border-gray-700"
                ></button>
              ) : (
                <button
                  className="bg-teal-500 text-white active:bg-zinc-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                  type="sumit"
                >
                  Update Password
                </button>
              )}
            </div>
          </form>
        </div>
      </div>
    </>
  );
};

export default ChangePasswordModal;
