import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";

const ViewCompanyModal = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const { services } = useSelector((state) => state.admin);
  const { companyForAction } = useSelector((state) => state.admin.utils);

  const [submitLoad, setSubmitLoad] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const [countries, setCoutries] = useState();

  const dispatch = useDispatch();

  const onSubmit = (userData) => {
    const formData = new FormData();
  };

  useEffect(() => {
    console.log(companyForAction);
  }, [companyForAction]);

  return (
    <>
      {companyForAction && (
        <>
          <input
            type="checkbox"
            id="view-company-modal"
            className="modal-toggle"
          />
          <div className="modal">
            <div className="modal-box relative">
              <label
                htmlFor="view-company-modal"
                className="btn btn-sm btn-circle absolute right-2 top-2"
              >
                
              </label>
              <h3 className="text-lg font-bold mb-6">Company Detail</h3>
              <div className="w-auto h-auto flex-col px-3">
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Name:{" "}
                    <span className="font-normal">
                      {" "}
                      {companyForAction[0].name}{" "}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Type:{" "}
                    <span className="font-normal">
                      {" "}
                      {companyForAction[0].category}
                    </span>
                  </h2>
                </div>

                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Number of Services:{" "}
                    <span className="font-normal">
                      {companyForAction[0].services.length}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Email:{" "}
                    <span className="font-normal">
                      {" "}
                      {companyForAction[0].email}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Tel:{" "}
                    <span className="font-normal">
                      {companyForAction[0].tel}
                    </span>
                  </h2>
                </div>
                <div className="w-full h-8 ">
                  <h2 className="font-semibold">
                    Status: <span className="font-normal">Active</span>
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ViewCompanyModal;
