import { useDispatch, useSelector } from "react-redux";
import { Link, Outlet } from "react-router-dom";
import { setCurrentServiceForAction } from "../../../features/company/companySlice";

const CompanyServiceMain = () => {
  return (
    <div className="w-full h-auto bg-zinc-100">
      <div className="w-full h-auto py-10 px-5 md:px-16 flex justify-center items-center flex-col space-y-2 bg-zinc-00">
        <CompanyServiceList />
      </div>
    </div>
  );
};

export const CompanyServiceList = () => {
  /*  const services = [
    { name: "Service 1", waitingTime: "10 mins", type: "Type 1", fee: "$10" },
    { name: "Service 2", waitingTime: "5 mins", type: "Type 2", fee: "$20" },
    { name: "Service 3", waitingTime: "15 mins", type: "Type 3", fee: "$30" },
    { name: "Service 4", waitingTime: "20 mins", type: "Type 4", fee: "$40" },
    { name: "Service 5", waitingTime: "30 mins", type: "Type 5", fee: "$50" },
  ]; */

  const { services } = useSelector((state) => state.company);

  const dispatch = useDispatch();

  const setTheCurrentService = (data) => {
    // dispatch the set cur staff
    //console.log(data);
    dispatch(setCurrentServiceForAction(data));
  };

  return (
    <div className="w-full h-auto min-h-24 flex justify-center items-center py-4 ">
      <div className="w-full h-auto flex justify-evenly items-center flex-row flex-wrap space-y-4 ">
        <div className="w-full h-16 flex justify-start items-center py-5 rounded-lg px-2">
          <div className="w-full h-auto flex flex-row justify-start items-center space-x-2 ">
            <h1 className="text-3xl font-bold">My Services</h1>
          </div>
          <div className="md:w-48 h-8  flex justify-center items-center rounded-md space-x-2">
            <label
              htmlFor="add-service-modal"
              className="text-white bg-teal-500 hover:bg-teal-600 focus:ring-1  focus:ring-red-300 font-bold rounded-lg text-md w-full px-2 py-2 text-center"
            >
              Add Service
            </label>
          </div>
        </div>
        <div className="w-full h-auto flex justify-evenly items-center flex-row flex-wrap ">
          <div className="w-full h-auto flex justify-evenly items-center flex-row flex-wrap py-10">
            {services && services.length && (
              <table className="table-auto w-full border rounded-md">
                <thead>
                  <tr>
                    <th className="px-4 py-2 text-start">Service name</th>
                    <th className="px-4 py-2 text-start">Type</th>
                    <th className="px-4 py-2 text-start">Service Time</th>
                    <th className="px-4 py-2 text-start">Fee</th>

                    <th className="px-4 py-2 w-72 "></th>
                  </tr>
                </thead>
                <tbody>
                  {services.map((service, index) => (
                    <tr
                      key={index}
                      className={index % 2 === 0 ? "bg-gray-100" : ""}
                    >
                      <td className="border-none px-4 py-2">{service.name}</td>
                      <td className="border-none px-4 py-2">{service.type}</td>
                      <td className="border-none px-4 py-2">
                        {service.waitTime}
                        <span> Minutes</span>
                      </td>
                      <td className="border-none px-4 py-2">
                        {service.fee} <span>ETB</span>
                      </td>
                      <td className="border-none px-4 py-2 text-center">
                        <label
                          onClick={() => setTheCurrentService(service._id)}
                          htmlFor="view-service-modal"
                          className="bg-teal-500 hover:bg-teal-600 text-white font-bold py-2 px-4 rounded mr-2"
                        >
                          View
                        </label>

                        <label
                          onClick={() => setTheCurrentService(service._id)}
                          htmlFor="edit-service-modal"
                          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mr-2"
                        >
                          Edit
                        </label>
                        <button
                          onClick={() => setTheCurrentService(service._id)}
                          className="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"
                        >
                          <a href="#delete-service-modal">Delete</a>
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default CompanyServiceMain;
